import React, { useContext, useEffect, useState } from 'react'
// import axios from 'axios'
import DefaultLayout from 'src/layouts/default'
import { MainHeading } from 'src/components/NavigationTabs'
import { AutoColumn } from 'src/components/Column'
import { useGroupActions } from 'src/context/GroupContext'
import { Text } from 'rebass'
import SliderPanel from 'src/components/SliderPanel'
import web3 from 'web3'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import Row, { RowBetween } from 'src/components/Row'
import { css } from '@emotion/core'
import { ButtonError } from 'src/components/Button'
import { useActiveWeb3React } from 'src/hooks'
import styled from 'styled-components'
import { ColumnCenter } from 'src/components/Column'
import { ChainId } from 'src/sdk'
import { ThemeContext } from 'styled-components'
import { ExternalLink, TYPE } from 'src/theme'
import { CloseIcon } from 'src/theme/components'
import { AlertTriangle, CheckCircle } from 'react-feather'
import { ButtonPrimary } from 'src/components/Button'
import { getEtherscanLink } from 'src/utils'
import Modal from 'src/components/Modal'
import { useTranslation } from 'react-i18next'
import { useCurrencyBalance } from 'src/state/wallet/hooks'
import { useCurrency } from 'src/hooks/Tokens'
// import VerifiedBadge from '../../assets/svg/verified-badge.png'
import { ErrorCard } from 'src/components/Card'
import { PageSpinner } from 'src/components/PageSpinner'
import { FlexBox } from 'src/components/FlexBox'
import { StyledTableRow, StyledTableCell } from 'src/components/TokensTable'
import numeral from 'numeral'

export const RowAlignRight = styled(Row)`
  justify-content: flex-end;
`

const Wrapper = styled.div`
  width: 100%;
`
const Section = styled(AutoColumn)`
  padding: 24px;
`

// const Panel = styled.div<{ hideInput?: boolean }>`
//   ${({ theme }) => theme.flexColumnNoWrap}
//   position: relative;
//   border-radius: ${({ hideInput }) => (hideInput ? '8px' : '20px')};
//   background-color: ${({ theme }) => theme.bg2};
//   z-index: 1;
// `

const BottomSection = styled(Section)`
  background-color: ${({ theme }) => theme.bg2};
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
`

const ConfirmedIcon = styled(ColumnCenter)`
  padding: 60px 0;
`

const StyledButtonWrapper = styled(FlexBox)`
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    flex-flow: column;
    width: 100%;
  `};
`

const StyledButtonError = styled(ButtonError)`
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    margin: 0 0 0.5rem;
    width: 100%;
  `};
`

function SuccessModalContent({
  onDismiss,
  chainId,
  groupId,
  hash
}: {
  onDismiss: () => void
  hash?: string | undefined
  chainId: ChainId
  groupId: number
}) {
  const { t } = useTranslation()
  const theme = useContext(ThemeContext)

  return (
    <Wrapper>
      <Section>
        <RowBetween>
          <div />
          <CloseIcon onClick={onDismiss} />
        </RowBetween>
        <ConfirmedIcon>
          <CheckCircle strokeWidth={1} size={64} color={theme.green1} />
        </ConfirmedIcon>
        <AutoColumn gap="12px" justify={'center'}>
          <Text fontWeight={600} fontSize={20}>
            {t('groupJoined', { groupId })}
          </Text>

          {chainId && hash && (
            <ExternalLink href={getEtherscanLink(chainId, hash, 'transaction')}>
              <Text fontWeight={400} fontSize={14} color={theme.primary1}>
                {t('View on Etherscan')}
              </Text>
            </ExternalLink>
          )}
          <ButtonPrimary onClick={onDismiss} style={{ margin: '20px 0 0 0' }}>
            {t('Close')}
          </ButtonPrimary>
        </AutoColumn>
      </Section>
    </Wrapper>
  )
}

export function ErrorModalContent({ message, onDismiss }: { message: string; onDismiss: () => void }) {
  const { t } = useTranslation()
  const theme = useContext(ThemeContext)
  return (
    <Wrapper>
      <Section>
        <RowBetween>
          <Text fontWeight={500} fontSize={20}>
            {t('Error')}
          </Text>
          <CloseIcon onClick={onDismiss} />
        </RowBetween>
        <AutoColumn style={{ marginTop: 20, padding: '2rem 0' }} gap="24px" justify="center">
          <AlertTriangle color={theme.red1} style={{ strokeWidth: 1 }} size={64} />
          <Text fontWeight={400} fontSize={16} color={theme.text1} style={{ textAlign: 'center', width: '85%' }}>
            {message}
          </Text>
        </AutoColumn>
      </Section>
      <BottomSection gap="12px">
        <ButtonPrimary onClick={onDismiss}>{t('Dismiss')}</ButtonPrimary>
      </BottomSection>
    </Wrapper>
  )
}

interface LocationState {
  backPath?: string
}

const GroupJoin = ({
  history,
  match: {
    params: { id }
  }
}: RouteComponentProps<{ id?: string }, any, LocationState | any>) => {
  const { t } = useTranslation()
  const theme = useContext(ThemeContext)
  const { getGroup, addFunds, approveJoinGroup, checkGroupAllowance } = useGroupActions()
  const { chainId } = useActiveWeb3React()
  const { account } = useActiveWeb3React()
  const [status, setStatus] = useState('unjoined')
  const [errorMessage, setErrorMessage] = useState<string>('')
  const [showModal, setShowModal] = useState(false)
  // const ethCurrency = useCurrency('ETH')
  // const ethBalance = useCurrencyBalance(account ?? undefined, ethCurrency ?? undefined)
  const uppCurrency = useCurrency(process.env.REACT_APP_UNIFUND_ADDRESS)
  const uppBalance = useCurrencyBalance(account ?? undefined, uppCurrency ?? undefined)
  // const [uppPrice, setUppPrice] = useState(0)

  useEffect(() => {
    if (status === 'joined') {
      setShowModal(true)
    } else if (errorMessage) {
      setShowModal(true)
    }
  }, [status, errorMessage])

  // useEffect(() => {
  //   axios(
  //     'https://api.coingecko.com/api/v3/simple/token_price/ethereum?contract_addresses=0xce25b4271cc4d937a7d9bf75b2068a7892b9961d&vs_currencies=eth'
  //   ).then((res: any) => {
  //     if (!res.data || !res.data[process.env.REACT_APP_UNIFUND_ADDRESS?.toLowerCase() as string]) return
  //     setUppPrice(res.data[process.env.REACT_APP_UNIFUND_ADDRESS?.toLowerCase() as string].eth)
  //   })
  // }, [])

  const onDismiss = () => {
    setErrorMessage('')
    setShowModal(false)
  }

  const group = getGroup(parseInt(id!))
  const actualMaxAllowed = group?.data
    ? parseFloat(web3.utils.fromWei(group?.data.maxEthAcceptable ?? '0')) -
      parseFloat(web3.utils.fromWei(group?.data.totalContributions ?? '0'))
    : 1
  const realMax = parseFloat(web3.utils.fromWei(group?.data.maxEthContribution ?? '0'))
  const minValue = 0
  const maxValue = actualMaxAllowed < realMax ? actualMaxAllowed : realMax

  const [amount, setAmount] = useState(0)
  const groupFull = minValue > maxValue

  useEffect(() => {
    checkGroupAllowance().then(() => {
      setStatus('checked')
    })
  }, [])

  const back = history.location.state?.backPath

  return (
    <>
      <MainHeading
        backPath={back ? back : '/'}
        title={t('Join fund')}
        helperText="If you have any trouble with this form, please contact us on Telegram."
      />
      <DefaultLayout
        isChildPage
        css={css`
          max-width: 800px;
          width: 100%;
        `}
      >
        <Modal isOpen={showModal} maxHeight={90} onDismiss={onDismiss}>
          {status === 'joined' ? (
            <SuccessModalContent chainId={chainId!} groupId={group!.id!} onDismiss={onDismiss} />
          ) : null}
          {errorMessage ? <ErrorModalContent message={errorMessage!} onDismiss={onDismiss} /> : null}
        </Modal>
        <FlexBox flex="column">
          {group ? (
            <>
              <h1>
                {group.params.description.length > 15
                  ? `${group.params.description.substr(0, 15)}...`
                  : group.params.description}
              </h1>
              {groupFull && (
                <FlexBox padding="1rem 0 0">
                  <ErrorCard>Fund is full and not accepting investments.</ErrorCard>
                </FlexBox>
              )}
              <FlexBox padding="1rem 0">
                <SliderPanel
                  disabled={groupFull}
                  label={t('Your investment')}
                  helpText="Enter the amount of ETH you wish to invest in the fund."
                  subLabel={`(${maxValue} ETH maximum)`}
                  unit="ETH"
                  value={amount}
                  min={minValue}
                  max={maxValue}
                  step={1}
                  onChange={(value: number) => {
                    setAmount(value!)
                  }}
                  onChangeInput={(event: any) => {
                    setAmount(parseFloat(event.target.value))
                  }}
                  id="field-addFunds"
                />
              </FlexBox>
              {uppBalance && parseFloat(uppBalance.toFixed(2)) < group.params.requiredMemberiFundFee && (
                <FlexBox padding="0 0 1.5rem">
                  <ErrorCard>
                    {numeral(group.params.requiredMemberiFundFee).format('0,0')} iFUND required to join. You have{' '}
                    {numeral(uppBalance.toFixed(2)).format('0,0')} iFUND.
                  </ErrorCard>
                </FlexBox>
              )}
              {amount > maxValue && (
                <FlexBox padding="0 0 1.5rem">
                  <ErrorCard>
                    You can only invest up to{' '}
                    {maxValue < group.params.maxEthContribution ? maxValue : group.params.maxEthContribution} ETH. Lower
                    your investment amount to continue.
                  </ErrorCard>
                </FlexBox>
              )}
              <FlexBox flex="column" padding="0.5rem 0 1.5rem">
                <h2>Cost</h2>
                <StyledTableRow>
                  <StyledTableCell width="50%">
                    <TYPE.body>Investment</TYPE.body>
                  </StyledTableCell>
                  <StyledTableCell width="50%">
                    <TYPE.body color={theme.text1} fontWeight={600}>{`${amount} ETH`}</TYPE.body>
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell width="50%">
                    <TYPE.body>Joining fee</TYPE.body>
                  </StyledTableCell>
                  <StyledTableCell width="50%">
                    <TYPE.body color={theme.text1} fontWeight={600}>{`${numeral(
                      group.params.requiredMemberiFundFee
                    ).format('0,0')} iFUND`}</TYPE.body>
                  </StyledTableCell>
                </StyledTableRow>
              </FlexBox>
              <FlexBox justify="flex-end">
                <StyledButtonWrapper>
                  {status === 'unjoined' || status === 'checking' ? (
                    <StyledButtonError
                      disabled={status === 'checking'}
                      onClick={async () => {
                        setStatus('checking')
                        if (await checkGroupAllowance()) {
                          setStatus('checked')
                        } else {
                          setStatus('unjoined')
                        }
                      }}
                      width="190px"
                    >
                      {status === 'checking' ? t('Checking...') : t('Check Allowance')}
                    </StyledButtonError>
                  ) : null}
                  {status !== 'unjoined' && status !== 'checking' ? (
                    <StyledButtonError
                      disabled={status !== 'unjoined' && status !== 'checking' && status !== 'checked'}
                      onClick={async () => {
                        setStatus('approving')
                        try {
                          await approveJoinGroup(group?.id!)
                          setStatus('approved')
                        } catch (e) {
                          setStatus('checked')
                          setErrorMessage(
                            t('Error approving the group. Please check the form and try again. Details: ') + e.message
                          )
                        }
                      }}
                      width="190px"
                    >
                      {status === 'approving'
                        ? t('Approving...')
                        : status === 'checked'
                        ? t('Approve iFUND Fee')
                        : t('Approved')}
                    </StyledButtonError>
                  ) : null}
                  {status !== 'joined' ? (
                    <StyledButtonError
                      disabled={status !== 'approved'}
                      onClick={async () => {
                        setStatus('joining')
                        try {
                          await addFunds(group?.id!, amount)
                          setStatus('joined')
                        } catch (e) {
                          setStatus('approved')
                          setErrorMessage(
                            t('Error joining the group. Please check the form and try again. Details: ') + e.message
                          )
                        }
                      }}
                      width="100px"
                      margin="0 0 0 0.75rem"
                    >
                      {status === 'joining' ? t('Joining...') : status === 'joined' ? t('Joined') : t('Join')}
                    </StyledButtonError>
                  ) : null}
                  {status === 'joined' ? (
                    <StyledButtonError disabled width="100px" margin="0 0 0 0.75rem">
                      {t('Joined')}
                    </StyledButtonError>
                  ) : null}
                </StyledButtonWrapper>
              </FlexBox>
            </>
          ) : (
            <PageSpinner />
          )}
        </FlexBox>
      </DefaultLayout>
    </>
  )
}

export default withRouter(GroupJoin)
