import GroupAbort from './pages/GroupAbort'
import GroupAddFunds from './pages/GroupAddFunds'
import GroupFinish from './pages/GroupFinish'
import GroupWithdraw from './pages/GroupWithdraw'
import GroupDetails from './pages/GroupDetails'
import GroupAddManager from './pages/GroupAddManager'
import GroupRemoveManager from './pages/GroupRemoveManager'
import Staking from './pages/Staking'
import Trades from './pages/Trades'
import Leaderboard from './pages/Leaderboard'
import axios from 'axios'
// import { darken } from 'polished'

const initConfig = async () => {
  const config = (
    await axios.get('config.json', {
      headers: {
        'Cache-Control': 'no-cache'
      }
    })
  ).data

  setInterval(async () => {
    const newConfig = (
      await axios.get('config.json', {
        headers: {
          'Cache-Control': 'no-cache'
        }
      })
    ).data

    if (newConfig.cacheBreaker !== config.cacheBreaker) {
      window.location.reload()
    }
  }, 1 * 60 * 1000)
}

initConfig()

const AppWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow-x: hidden;
`

const HeaderWrapper = styled.div`
  ${({ theme }) => theme.flexRowNoWrap}
  width: 100%;
  justify-content: space-between;
`

const BodyWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 64px 16px 0;
  align-items: center;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    padding-top: 48px;
  `};
`

