export default function App() {
  return (
    <Suspense fallback={null}>
      <Router>
        <Route component={GoogleAnalyticsReporter} />
        <Route component={DarkModeQueryParamReader} />
        <AppWrapper>
          <HeaderWrapper>
            <Header />
          </HeaderWrapper>
          <HeaderBanner
            message="Win big in our first trading competition!"
            linkText="Register Now"
            linkUrl="http://bit.ly/ifundrsvp"
          />
          <BodyWrapper>
            {/* <RegistrationLink href="http://bit.ly/ifundrsvp">
              <div>Unifund Trading Competition</div>
              <div
                css={css`
                  font-size: 1.25em;
                `}
              >
                Early Registration Open Now
              </div>
            </RegistrationLink> */}
            <Popups />
            <Web3ReactManager>
              <Switch>
                <Route exact strict path="/group" component={GroupCreate} />
                <Route exact strict path="/group/:id/swap" component={GroupSwap} />
                <Route exact strict path="/group/:id/join" component={GroupJoin} />
                <Route exact strict path="/group/:id/start" component={GroupStart} />
                <Route exact strict path="/group/:id/abort" component={GroupAbort} />
                <Route exact strict path="/group/:id/add-funds" component={GroupAddFunds} />
                <Route exact strict path="/group/:id/finish" component={GroupFinish} />
                <Route exact strict path="/group/:id/withdraw" component={GroupWithdraw} />
                <Route exact strict path="/group/:id/details" component={GroupDetails} />
                <Route exact strict path="/group/:id/add-manager" component={GroupAddManager} />
                <Route exact strict path="/group/:id/remove-manager" component={GroupRemoveManager} />
                <Route exact strict path="/trades" component={Trades} />
                <Route exact strict path="/staking" component={Staking} />
                <Route exact strict path="/leaderboard" component={Leaderboard} />
                {/* <Route exact strict path="/open" component={Home} />
                <Route exact strict path="/closed" component={Home} /> */}
                <Route component={Home} />
              </Switch>
            </Web3ReactManager>
            <div
              css={css`
                margin-top: 5rem;
              `}
            />
          </BodyWrapper>
        </AppWrapper>
      </Router>
    </Suspense>
  )
}
