import React, { useContext } from 'react'
import styled, { css, ThemeContext } from 'styled-components'
import { Award } from 'react-feather'
import DefaultLayout from 'src/layouts/default'
import { withRouter, RouteComponentProps } from 'react-router-dom'

const StyledHeaderWrapper = styled.div`
  padding-bottom: 1.5rem;
`

const StyledMyFundsEmptyStateWrapper = styled.div`
  border: ${({ theme }) => `1px solid ${theme.bg3}`};
  border-radius: 1rem;
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 2.5rem;
  text-align: center;
`

const StyledEmptyStateIconWrapper = styled.div`
  background: linear-gradient(135deg, #fdf69a 0%, #95fff1 50%, #9598ff 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  height: 3rem;
  width: 3rem;
`

const Leaderboard = ({ history }: RouteComponentProps) => {
  const theme = useContext(ThemeContext)
  return (
    <DefaultLayout
      css={css`
        max-width: 800px;
        width: 100%;
      `}
    >
      <StyledHeaderWrapper>
        <h1>Leaderboard</h1>
      </StyledHeaderWrapper>
      <StyledMyFundsEmptyStateWrapper>
        <StyledEmptyStateIconWrapper>
          <Award color={theme.black} size="1.5rem" />
        </StyledEmptyStateIconWrapper>
        <h2>Leaderboard is coming soon!</h2>
      </StyledMyFundsEmptyStateWrapper>
    </DefaultLayout>
  )
}

export default withRouter(Leaderboard)
