import React, { useState } from 'react'
import DefaultLayout from 'src/layouts/default'
import { MainHeading } from 'src/components/NavigationTabs'
import { AutoColumn } from 'src/components/Column'
import { useGroupActions } from 'src/context/GroupContext'
import AddressInputPanel from 'src/components/AddressInputPanel'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import { RowBetween, AutoRow } from 'src/components/Row'
import { css } from '@emotion/core'
import { ButtonError } from 'src/components/Button'
import { isAddress, shortenAddress } from 'src/utils'
import Loader from 'src/components/Loader'
import InfoModal from 'src/components/InfoModal'
import styled from 'styled-components'

const Description = styled.div`
  padding 1em 0;
  line-height: 1.25em;
  text-align: left;
`
const GroupAddManager = ({
  history,
  match: {
    params: { id }
  }
}: RouteComponentProps<{ id?: string }>) => {
  const { getGroup, isGroupManager, addGroupManager } = useGroupActions()
  const [manager, setManager] = useState('')
  const [attempting, setAttempting] = useState(false)
  const [isOpen, setIsOpen] = useState(false)
  const [isError, setIsError] = useState(false)
  const [statusMessage, setStatusMessage] = useState('')
  const group = getGroup(parseInt(id!))

  const onAddManager = async () => {
    setAttempting(true)
    try {
      const result = await isGroupManager(group!.id!, manager)
      if (result) {
        setStatusMessage(`Address ${shortenAddress(manager)} is already registered as a manager for this group`)
      }
      else {
        await addGroupManager(group?.id!, manager)
        setStatusMessage("Manager added")
      }
      setIsError(false)
    }
    catch (e) {
      setStatusMessage(e.message ?? e.reason ?? "Error occurred")
      setIsError(true)
    }
    setIsOpen(true)
    setAttempting(false)
  }

  return (
    <DefaultLayout>
      <InfoModal isOpen={isOpen} onDismiss={() => setIsOpen(false)} isError={isError} message={statusMessage} />
      <MainHeading backPath="/" title="Add Manager" />
      
      <AutoColumn
        gap="md"
        css={css`
          text-align: center;
        `}
      >
        {group ? (
          <>
            <Description>A manager is authorized to trade on behalf of the group leader</Description>
            <AddressInputPanel
              label="Address"
              placeholder=""
              showEtherscanLink={false}
              value={manager}
              onChange={setManager}
              id="field-addManager"
            />

            <RowBetween>
              <div
                css={css`
                  width: 100%;
                  padding: 5px;
                  margin: 0 -5px 0 0;
                `}
              >
                <ButtonError disabled={attempting || !isAddress(manager)} onClick={() => onAddManager()}>
                  {
                    attempting
                      ? (
                        <AutoRow gap="0.5em" justify="center">
                          Adding...
                          <Loader stroke="white" />
                        </AutoRow>
                      )
                      : "Add Manager"
                  }
                </ButtonError>
              </div>
            </RowBetween>
          </>
        ) : (
            <div
              css={css`
              padding: 10px;
            `}
            >
              Group is not found
            </div>
          )}
      </AutoColumn>
    </DefaultLayout>
  )
}

export default withRouter(GroupAddManager)
