import React from 'react'
import styled from 'styled-components'
import { Spinner } from '../../theme/components'
import { css } from '@emotion/core'
import { useActiveWeb3React } from 'src/hooks'
import { useWalletModalToggle } from 'src/state/application/hooks'
import { AutoColumn } from 'src/components/Column'
import { ButtonError } from 'src/components/Button'
import { TYPE } from 'src/theme'
import Circle from '../../assets/images/blue-loader.svg'

const StyledSpinnerWrapper = styled.div`
  display: flex;
  justify-content: center;
  padding: 2rem 0;
`

export const PageSpinner = () => {
  const toggleWalletModal = useWalletModalToggle()
  const { account } = useActiveWeb3React()
  return !account ? (
    <AutoColumn
      gap="md"
      css={css`
        display: flex;
        flex-flow: column;
        align-items: center;
      `}
    >
      <h1>Let the fund begin</h1>
      <TYPE.body>Connect your wallet to join and create funds.</TYPE.body>
      <ButtonError margin="1rem 0 0 0" width="150px" onClick={toggleWalletModal}>
        Connect Wallet
      </ButtonError>
    </AutoColumn>
  ) : (
    <StyledSpinnerWrapper>
      <Spinner src={Circle} />
    </StyledSpinnerWrapper>
  )
}
