import { formatEther } from 'ethers/lib/utils'
import ERC20Abi from '../constants/abis/erc20.json'

export const getBalanceByPoolId = async (poolId: number, web3: any, account: string) => {
  const tokenAddress = poolId === 0 ? process.env.REACT_APP_UNIFUND_ADDRESS! : process.env.REACT_APP_LP_ADDRESS!
  const erc20Contract = new web3.eth.Contract(ERC20Abi, tokenAddress)
  return parseFloat(formatEther(await erc20Contract.methods.balanceOf(account).call({ from: account })))
}