// @ts-ignore
window.GroupService = GroupService

type GroupParamsType = {
  tokenListId: number
  maxRunTimeHours: number
  startTimeoutHours: number
  leaderProfitShareOutOf10000: number
  leaderiFundCollateral: number
  requiredMemberiFundFee: number
  maxEthContribution: number
  minEthToStart: number
  maxEthAcceptable: number
  description: string
  name: string
}

type GroupStatus =
  | 'uncreated'
  | 'openForm'
  | 'checkAllowance'
  | 'checkingAllowance'
  | 'readyToApprove'
  | 'readyToCreate'
  | 'startApprove'
  | 'startCreate'
  | 'approving'
  | 'creating'
  | 'created'
  | 'failed'

type GroupAdditionalInfo = {
  name?: string
  description?: string
}

type GroupToken = {
  address: string
  name: string
  amount: number
}

type GroupType = {
  id?: number
  name?: string
  txHash?: string
  errorMessage?: string
  status: GroupStatus
  params: GroupParamsType
  withdrawn?: boolean
  totalContributions?: number
  myContribution?: number
  data?: any
  tokens: GroupToken[] | undefined
  verified?: boolean
  additionalInfo?: GroupAdditionalInfo
  isGroupManager?: boolean
}

export type State = {
  groupForm: GroupType
  groups: GroupType[]
}

export type Actions = {
  getState: () => any
  editGroupForm: (group: Partial<GroupType>) => void
  updateGroup: (group: GroupType) => void
  startEdit: () => void
  updateStatus: (status: GroupStatus) => void
  clearForm: () => void
  startApprove: () => void
  startCreate: () => void
  checkAllowance: () => void
  checkGroupAllowance: () => Promise<boolean>
  setGroupParams: (params: Partial<GroupParamsType>) => void
  getGroup: (groupId: number) => GroupType | undefined
  addOrUpdateGroup: (groupId: number, group?: any) => void
  onGroupUpdated: (eventName: string, groupId: number, transactionHash: string) => void
  startGroup: (groupId: number) => void
  finishGroup: (groupId: number) => void
  abortGroup: (groupId: number) => void
  approveJoinGroup: (groupId: number) => void
  addFunds: (groupId: number, amount: number) => void
  withdraw: (groupId: number) => void
  reloadGroups: () => void
  getTokenList: (tokenListId: number) => any
  saveData: (key: string, value: string, type: string) => Promise<unknown>
  updateGroupInfo: ({ groupId, name, description }: { groupId: number; name: string; description: string }) => void
  fetchAdditionalInfo: () => void
  addGroupManager: (groupId: number, manager: string) => void
  removeGroupManager: (groupId: number, manager: string) => void
  isGroupManager: (groupId: number, manager: string) => any
}

type Action = {
  key: string
  value: GroupType
}

export const GroupStateContext = createContext<State | undefined>(undefined)
export const GroupActionsContext = createContext<Actions | undefined>(undefined)

function GroupReducer(state: State, action: Action): State {
  console.debug('GroupReducer', action, action.value)

  if (action.key === 'editGroupForm') {
    return {
      ...state,
      groupForm: action.value
    }
  } else if (action.key === 'addOrUpdateGroup') {
    const index = state.groups.findIndex(x => x.id === action.value.id)

    if (index !== -1) {
      const newGroups = [...state.groups]
      newGroups[index] = action.value

      return {
        ...state,
        groups: newGroups
      }
    } else {
      return {
        ...state,
        groups: [...state.groups, action.value]
      }
    }
  }

  return state
}

