const GroupProvider: React.FC<GroupProviderProps> = ({ value, children }) => {
  const [state, dispatch, getState] = useEnhancedReducer(GroupReducer, {
    groupForm: value || defaultGroupForm,
    groups: []
  })

  // @ts-ignore
  window.unistate = state

  const { account } = useActiveWeb3React()
  const web3 = useOldWeb3()
  const tokenContract = useContract(process.env.REACT_APP_UNIFUND_ADDRESS!, ERC20Abi)
  const groupContract = useContract(process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!, UnifundGroupAbi) //getContract(process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!, UnifundGroupAbi, library!, account!)
  const addTransaction = useTransactionAdder()

  const setGroupParams = (params: Partial<GroupParamsType>) => {
    const state = getState()
    const sanitizedParams: { [key: string]: any } = params

    for (const key in sanitizedParams) {
      if (typeof sanitizedParams[key] === 'number' && isNaN(sanitizedParams[key])) {
        sanitizedParams[key] = 0
      }
    }

    dispatch({
      key: 'editGroupForm',
      value: {
        ...state.groupForm,
        status: 'openForm',
        params: {
          ...state.groupForm.params,
          ...sanitizedParams
        }
      }
    })
  }

  const editGroupForm = (group: Partial<GroupType>) =>
    dispatch({
      key: 'editGroupForm',
      value: {
        ...getState().groupForm,
        ...group
      }
    })

  const updateStatus = (status: GroupStatus) =>
    dispatch({
      key: 'editGroupForm',
      value: {
        ...getState().groupForm,
        status: status
      }
    })

  const startEdit = () =>
    dispatch({
      key: 'editGroupForm',
      value: {
        ...getState().groupForm,
        status: 'openForm'
      }
    })

  const startApprove = () => {
    updateStatus('approving')

    tokenContract
      ?.approve(process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!, parseEther('30000000'), {
        from: account
      })
      .then((tx: any) => {
        addTransaction(tx)
        updateStatus('readyToCreate')
        // tx.wait().then((txReceipt: any) => {
        //   if (txReceipt.status === 1) {
        //     updateStatus('readyToCreate')
        //   } else {
        //     updateStatus('readyToApprove')
        //   }
        // })

        return tx.hash
      })
      .catch((error: any) => {
        updateStatus('readyToApprove')
        // if the user rejected the tx, pass this along
        if (error?.code !== 4001) {
          // otherwise, the error was unexpected and we need to convey that
          console.error(`Approve group failed`, error)
          throw new Error(`Approve group failed: ${error.message}`)
        }
      })
  }

  const startCreate = () => {
    updateStatus('creating')
    const { groupForm } = state
    const { params } = groupForm
    const tokenListId = BigNumber.from(params.tokenListId)
    const maxRunTimeHours = BigNumber.from(params.maxRunTimeHours)
    const startTimeoutHours = BigNumber.from(params.startTimeoutHours)
    const leaderProfitShareOutOf10000 = BigNumber.from(params.leaderProfitShareOutOf10000)
    const leaderiFundCollateral = parseEther(params.leaderiFundCollateral.toString())
    const requiredMemberiFundFee = parseEther(params.requiredMemberiFundFee.toString())
    const maxEthContribution = parseEther(params.maxEthContribution.toString())
    const minEthToStart = parseEther(params.minEthToStart.toString())
    const maxEthAcceptable = parseEther(params.maxEthAcceptable.toString())
    const description = params.description

    groupContract
      ?.create(
        tokenListId,
        maxRunTimeHours,
        startTimeoutHours,
        leaderProfitShareOutOf10000,
        leaderiFundCollateral,
        requiredMemberiFundFee,
        maxEthContribution,
        minEthToStart,
        maxEthAcceptable,
        description,
        {
          from: account
        }
      )
      .then((tx: any) => {
        const { groupForm } = getState()
        //const { name, description } = groupForm.additionalInfo

        addTransaction(tx)

        editGroupForm({
          ...groupForm,
          txHash: tx.hash
        })

        tx.wait().then((txReceipt: any) => {
          const { groupForm } = getState()

          if (txReceipt.status === 1) {
            const txEvent = txReceipt.events.pop()

            const groupId: number = txEvent.args['groupId'].toNumber()

            addOrUpdateGroup(groupId)

            editGroupForm({
              ...groupForm,
              status: 'created',
              id: groupId
            })

            // updateGroupInfo({
            //   groupId,
            //   name,
            //   description
            // })
          } else {
            editGroupForm({
              ...groupForm,
              status: 'failed',
              errorMessage: 'Error creating the group. Please check the form and try again.'
            })
          }

          // addPopup(
          //   {
          //     txn: {
          //       hash,
          //       success: receipt.status === 1,
          //       summary: transactions[hash]?.summary
          //     }
          //   },
          //   hash
          // )
        })

        return tx.hash
      })
      .catch((error: any) => {
        updateStatus('readyToCreate')
        // if the user rejected the tx, pass this along
        if (error?.code !== 4001) {
          // otherwise, the error was unexpected and we need to convey that
          console.error(`Create group failed`, error)
          const { groupForm } = getState()
          editGroupForm({
            ...groupForm,
            status: 'failed',
            errorMessage: `Error creating the group. Please check the form and try again. Details: ${error.message}`
          })
        }
      })
  }

  const getGroup = (groupId: number): GroupType | undefined => {
    const state = getState()
    return state.groups.find((x: any) => x.id === groupId)
  }

  const updateGroup = (group: GroupType) =>
    dispatch({
      key: 'addOrUpdateGroup',
      value: group
    })

  const clearForm = () =>
    dispatch({
      key: 'uncreated',
      value: defaultGroupForm
    })

  const onGroupUpdated = async (eventName: string, groupId: number, transactionHash: string) => {
    if (latestTransactionHash === transactionHash) {
      // ignore duplicate events
      return
    }

    latestTransactionHash = transactionHash

    if (state.groupForm.txHash === transactionHash) {
      clearForm()
    }

    await addOrUpdateGroup(groupId)
  }

  const addOrUpdateGroup = async (groupId: number, group?: any) => {
    if (!group) {
      group = await getGroupFromContract(account!, groupContract!, groupId)
    }

    // const tokens = []
    // const tokenCount = await GroupService.tokenListTokenCount(group.data.tokenListId, web3, account!)

    // for (let i = 0; i < tokenCount; i++) {
    //   const tokenAddress = await GroupService.tokenListToken(group.data.tokenListId, i, web3, account!)
    //   const tokenName = await GroupService.getTokenName(tokenAddress, web3, account!)

    //   tokens.push({
    //     name: tokenName,
    //     address: tokenAddress
    //   })
    // }
    setTimeout(() => {
      GroupService.tokensWithBalances(groupId, web3, account!).then(async tokenAddresses => {
        const tokens = []
        for (const tokenAddress of tokenAddresses) {
          const tokenBalance = await GroupService.groupBalance(groupId, tokenAddress, web3, account!)
          const tokenName = await GroupService.getTokenName(tokenAddress, web3, account!)
          const tokenSymbol = await GroupService.getTokenSymbol(tokenAddress, web3, account!)
          if (tokenBalance > 0) {
            tokens.push({
              name: tokenName,
              address: tokenAddress,
              symbol: tokenSymbol,
              amount: parseFloat(formatEther(tokenBalance))
            })
          }
        }
        const group = getGroup(groupId)

        dispatch({
          key: 'addOrUpdateGroup',
          value: {
            ...group,
            tokens
          }
        })
      })
    }, 100)

    dispatch({
      key: 'addOrUpdateGroup',
      value: {
        id: groupId,
        status: 'created',
        params: {
          description: group.description,
          tokenListId: group.tokenListId.toNumber(),
          maxRunTimeHours: group.maxRunTimeHours,
          startTimeoutHours: group.startTimeoutHours, // todo fix 1600048707
          leaderProfitShareOutOf10000: group.leaderProfitShareOutOf10000,
          leaderiFundCollateral: parseFloat(formatEther(group.leaderiFundCollateral)),
          requiredMemberiFundFee: parseFloat(formatEther(group.requiredMemberiFundFee)),
          maxEthContribution: parseFloat(formatEther(group.maxEthContribution)),
          minEthToStart: parseFloat(formatEther(group.minEthToStart)),
          maxEthAcceptable: parseFloat(formatEther(group.maxEthAcceptable))
        },
        data: group.data,
        withdrawn: group.withdrawn,
        isGroupManager: group.isGroupManager,
        myContribution: parseFloat(formatEther(group.myContribution)),
        totalContributions: parseFloat(formatEther(group.totalContributions)),
        tokens: group.tokens
      }
    })
  }

  const startGroup = async (groupId: number) => {
    await GroupService.startGroup(groupId, web3, account!)
  }

  const finishGroup = async (groupId: number) => {
    await GroupService.finishGroup(groupId, web3, account!)
  }

  const abortGroup = async (groupId: number) => {
    await GroupService.abortGroup(groupId, web3, account!)
  }

  const checkGroupAllowance = async () => {
    return await GroupService.checkGroupAllowance(state.groupForm.params.leaderiFundCollateral, web3, account!)
  }

  const approveGroup = async (groupId: number) => {
    await GroupService.approveGroup(
      parseEther('30000000'), //group.data.requiredMemberiFundFee,
      web3,
      account!
    )
  }

  const approveJoinGroup = async (groupId: number) => {
    const group = getGroup(groupId)!

    await GroupService.approveJoinGroup(
      groupId,
      parseEther('30000000'), //group.data.requiredMemberiFundFee,
      AccountService.isGroupLeader(group, account!),
      web3,
      account!
    )
  }

  const addFunds = async (groupId: number, value: number) => {
    const group = getGroup(groupId)!
    await GroupService.joinGroup(
      groupId,
      value,
      parseEther('30000000'), //group.data.requiredMemberiFundFee,
      AccountService.isGroupLeader(group, account!),
      web3,
      account!
    )
  }

  const withdraw = async (groupId: number) => {
    const group = getGroup(groupId)!
    await GroupService.withdraw(groupId, group.data.aborted, web3, account!)
    await addOrUpdateGroup(groupId)
  }

  const checkAllowance = async () => {
    updateStatus('checkingAllowance')
    const allowance = await tokenContract?.allowance(account!, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!, {
      from: account
    })
    console.log('mmm', allowance.toString())
    if (allowance.gte(state.groupForm.params.leaderiFundCollateral)) {
      updateStatus('readyToCreate')
    } else {
      updateStatus('readyToApprove')
    }
  }

  const addGroupManager = async (groupId: number, manager: string) =>
    await GroupService.addAuthorizedTrader(groupId, manager, web3, account!)
  const removeGroupManager = async (groupId: number, manager: string) =>
    await GroupService.removeAuthorizedTrader(groupId, manager, web3, account!)
  const isGroupManager = async (groupId: number, manager: string) =>
    await GroupService.isAuthorizedTrader(groupId, manager, web3, account!)

  const getTokenList = async (tokenListId: number) => {
    return GroupService.getTokenList(tokenListId, web3, account!)
  }

  // @ts-ignore
  window.addOrUpdateGroup = addOrUpdateGroup

  const reloadGroups = async () => {
    if (!web3 || !account) return

    const { otherGroups } = await loadGroups(web3, account!)

    otherGroups
      // openGroups
      // .concat(runningGroups)
      // .filter((item: any) => !myGroups.find((x: any) => x.groupId === item.groupId))
      // .concat(myGroups)
      .filter((item: any) => item.group.tokenListId === '0')
      .forEach((item: any) => {
        addOrUpdateGroup(parseInt(item.groupId), {
          id: parseInt(item.groupId),
          description: item.group.description,
          tokenListId: BigNumber.from(item.group.tokenListId),
          maxRunTimeHours: item.group.maxRunTimeHours,
          startTimeoutHours: item.group.startTimeout,
          leaderProfitShareOutOf10000: item.group.leaderProfitShareOutOf10000,
          leaderiFundCollateral: BigNumber.from(item.group.leaderiFundCollateral),
          requiredMemberiFundFee: BigNumber.from(item.group.requiredMemberiFundFee),
          maxEthContribution: BigNumber.from(item.group.maxEthContribution),
          minEthToStart: BigNumber.from(item.group.minEthToStart),
          maxEthAcceptable: BigNumber.from(item.group.maxEthAcceptable),
          withdrawn: item.withdrawn,
          isGroupManager: item.isGroupManager,
          myContribution: BigNumber.from(item.myContribution),
          totalContributions: BigNumber.from(item.group.totalContributions),
          leader: item.group.leader,
          data: item.group
        })
      })
  }

  const saveData = async (key: string, value: string, type: string) => {
    return new Promise((resolve, reject) => {
      // send .key and .value to sheet
      const bodyFormData = new FormData()

      bodyFormData.set('entry.2067540436', key)
      bodyFormData.set('entry.401305064', value)
      bodyFormData.set('entry.712061620', type)

      axios('https://docs.google.com/forms/d/1FAIpQLSfZuxxsugOdrWHpucfjx1Mavjl6x8_vI66ZbiMFFpD9LZ5wvw/formResponse', {
        method: 'post',
        data: bodyFormData,
        headers: { 'Content-Type': 'multipart/form-data' }
      })
        .then(res => {
          resolve()
        })
        .catch(err => {
          reject('An error occurred.')
        })
    })
  }

  const fetchAdditionalInfo = async () => {
    const sheetUrl =
      'https://spreadsheets.google.com/feeds/list/1TBlZP_LNmn_v-ZasvU8B7hSTjGbOSO5_oGsgkgRs588/1/public/values?alt=json'

    axios(sheetUrl, {
      method: 'get'
    })
      .then(res => {
        const entries = res.data.feed.entry
        try {
          for (const i in entries) {
            const entry = entries[i]
            const key = entry.gsx$key.$t
            const type = entry.gsx$type.$t
            const verified = entry.gsx$verified.$t === '1'
            let value = entry.gsx$value.$t
            if (type === 'int32') {
              value = Number(value)
            } else if (type === 'boolean') {
              value = Boolean(value)
            } else if (type === 'json') {
              value = JSON.parse(value)
            }

            if (key === 'additionalGroupInfo') {
              const additionalInfo = value.data
              const group = getGroup(value.groupId)!

              if (group) {
                const signingAddress = sigUtil.recoverPersonalSignature({
                  data: 'Additional Info: ' + JSON.stringify(additionalInfo, undefined, 4),
                  sig: value.signature
                })
                //const signingAddress = web3!.personal.recover('Update: additionalGroupInfo', additionalInfo.signature)
                if (signingAddress.toLowerCase() === group.data.leader.toLowerCase()) {
                  dispatch({
                    key: 'addOrUpdateGroup',
                    value: {
                      ...group,
                      verified,
                      additionalInfo: {
                        ...group.additionalInfo,
                        ...additionalInfo
                      }
                    }
                  })
                }
              }
            }
          }
        } catch (e) {
          console.log(e)
        }
      })
      .catch(err => {
        console.log(
          `Could not contact external state service. Please contact support with this error: ${JSON.stringify(err)}`
        )
      })
  }

  const updateGroupInfo = async ({
    groupId,
    name,
    description
  }: {
    groupId: number
    name: string
    description: string
  }) => {
    const additionalInfo = {
      name,
      description
    }
    const from = account!
    const text = 'Additional Info: ' + JSON.stringify(additionalInfo, undefined, 4)
    const msg = ethUtil.bufferToHex(new Buffer(text, 'utf8'))
    const params = [msg, from]
    const method = 'personal_sign'

    // @ts-ignore
    return web3!.currentProvider!.sendAsync(
      {
        method,
        params,
        from
      },
      function(err: any, result: any) {
        if (err) return console.error(err)
        if (result.error) return console.error(result.error)

        const signature = result.result

        const additionalInfo = {
          groupId,
          signature,
          data: {
            name,
            description
          }
        }
        console.debug('Saving data', JSON.stringify(additionalInfo))
        //saveData('additionalGroupInfo', JSON.stringify(additionalInfo), 'json')
      }
    )
    // @ts-ignore
    //const signature = await web3!.personal.sign('Update: additionalGroupInfo', account!.toLowerCase())
  }

  useEffect(() => {
    async function init() {
      await reloadGroups()
      //await fetchAdditionalInfo()
    }

    init()
  }, [account])

  const actions = {
    getState,
    getGroup,
    reloadGroups,
    clearForm,
    addOrUpdateGroup,
    updateGroup,
    editGroupForm,
    setGroupParams,
    checkAllowance,
    startEdit,
    updateStatus,
    startApprove,
    startCreate,
    onGroupUpdated,
    startGroup,
    finishGroup,
    abortGroup,
    checkGroupAllowance,
    approveGroup,
    approveJoinGroup,
    addFunds,
    withdraw,
    getTokenList,
    updateGroupInfo,
    saveData,
    fetchAdditionalInfo,
    addGroupManager,
    removeGroupManager,
    isGroupManager
  }

  return (
    <GroupStateContext.Provider value={state}>
      <GroupActionsContext.Provider value={actions}>{children}</GroupActionsContext.Provider>
    </GroupStateContext.Provider>
  )
}

function useGroupState() {
  const state = React.useContext(GroupStateContext)

  if (state === undefined) {
    throw new Error('useGroupState must be used within a GroupProvider')
  }

  return state
}

function useGroupActions() {
  const actions = React.useContext(GroupActionsContext)

  if (actions === undefined) {
    throw new Error('useGroupActions must be used within a GroupProvider')
  }

  return actions
}

function useGroup(): [State, Actions] {
  return [useGroupState(), useGroupActions()]
}

interface ConsumerProps {
  children: (state: State | undefined, actions: Actions | undefined) => React.ReactNode
}
const GroupConsumer: React.FC<ConsumerProps> = ({ children }) => {
  return (
    <GroupStateContext.Consumer>
      {state => {
        if (state === undefined) {
          throw new Error('GroupConsumer must be used within a GroupProvider')
        }
        return (
          <GroupActionsContext.Consumer>
            {actions => {
              if (actions === undefined) {
                throw new Error('GroupConsumer must be used within a GroupProvider')
              }
              return children(state, actions)
            }}
          </GroupActionsContext.Consumer>
        )
      }}
    </GroupStateContext.Consumer>
  )
}

// TODO: get rid of this bullshit later
function GroupUpdater() {
  const { groupForm } = useGroupState()
  const { onGroupUpdated, reloadGroups, editGroupForm } = useGroupActions()
  const groupContract = useContract(process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!, UnifundGroupAbi) //getContract(process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!, UnifundGroupAbi, library!, account!)

  useEffect(() => {
    groupContract?.on('*', async (event: any) => {
      console.log('eventHandler', event, event.args)
      if (event.event === 'GroupCreated') {
        const groupId: BigNumber = event.args.groupId

        onGroupUpdated(event.event, groupId.toNumber(), event.transactionHash)

        if (event.transactionHash === groupForm.txHash) {
          editGroupForm({
            ...groupForm,
            status: 'created',
            id: groupId.toNumber()
          })
        }
      } else if (['GroupStarted', 'GroupAborted', 'GroupFinished', 'GroupContribution'].includes(event.event)) {
        reloadGroups()
      }
    })
  }, [groupContract])

  return null
}

export { GroupProvider, GroupConsumer, GroupUpdater, useGroup, useGroupState, useGroupActions }
