import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import { ChevronRight } from 'react-feather'
import { TYPE } from '../../theme'
import { FlexBox } from '../FlexBox'

const StyledFlexBox = styled(FlexBox)`
  background: ${({ theme }) => theme.primary1};
  background: ${({ theme }) => `linear-gradient(135deg, ${theme.primary1} 0%,${theme.primary2} 100%)`};
  margin-top: 56px;
  width: 100%;

  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    flex-flow: column;
  `};
`

const StyledLink = styled.a`
  border: ${({ theme }) => `1px solid ${theme.white}`};
  border-radius: 0.25rem;
  margin-left: 1rem;
  padding: 0.125rem 0.5rem;
  text-decoration: none;

  :hover {
    background: rgba(255, 255, 255, 0.1);
  }

  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    margin-left: 0;
    margin-top: 0.5rem;
  `};
`

interface HeaderBannerProps {
  message: string
  linkUrl?: string
  linkText?: string
}

export const HeaderBanner = ({ message, linkText, linkUrl }: HeaderBannerProps) => {
  const theme = useContext(ThemeContext)
  return (
    <StyledFlexBox align="center" padding="0.5rem 1rem" justify="center">
      <TYPE.main color={theme.white} fontSize="14px">
        {message}
      </TYPE.main>
      {linkUrl && (
        <StyledLink href={linkUrl}>
          <FlexBox align="center">
            <TYPE.body color={theme.white} fontSize="14px" fontWeight="600" marginRight="0.25rem">
              {linkText}
            </TYPE.body>
            <ChevronRight color={theme.white} size="16px" />
          </FlexBox>
        </StyledLink>
      )}
    </StyledFlexBox>
  )
}
