import React, { useRef } from 'react'
import { Info, BookOpen, MessageCircle, Twitter } from 'react-feather'
import styled from 'styled-components'
import { ReactComponent as MenuIcon } from '../../assets/images/menu.svg'
import { useOnClickOutside } from '../../hooks/useOnClickOutside'
import useToggle from '../../hooks/useToggle'
import { GroupStakingTabs } from '../NavigationTabs'
import { ExternalLink } from '../../theme'
import { RouteComponentProps, withRouter } from 'react-router-dom'

const StyledMenuIcon = styled(MenuIcon)`
  height: 20px;
  width: 20px;
  line {
    stroke: ${({ theme }) => theme.text1};
  }
`

const StyledMenuButton = styled.button`
  width: 2.5rem;
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  height: 2.5rem;
  background-color: ${({ theme }) => theme.bg2};

  border-radius: 0.25rem;

  :hover,
  :focus {
    cursor: pointer;
    outline: none;
    background-color: ${({ theme }) => theme.bg4};
  }

  svg {
    margin-top: 2px;
  }
`

const StyledMenu = styled.div`
  margin-left: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  text-align: left;
`

const MenuFlyout = styled.span`
  min-width: 8.125rem;
  background-color: ${({ theme }) => theme.bg3};
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 0.5rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  position: absolute;
  top: 3rem;
  right: 0rem;
  z-index: 100;

  ${({ theme }) => theme.mediaWidth.upToMedium`
    min-width: 12rem;
  `};
`

const MenuItem = styled(ExternalLink)`
  display: flex;
  align-items: center;
  flex: 1;
  padding: 0.5rem 0;
  color: ${({ theme }) => theme.text2};
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
`

const MainNavLinksWrapper = styled.div`
  display: none;
  ${({ theme }) => theme.mediaWidth.upToMedium`
    display: block;
  `};
`

function Menu({ location }: RouteComponentProps) {
  const node = useRef<HTMLDivElement>()
  const [open, toggle] = useToggle(false)

  useOnClickOutside(node, open ? toggle : undefined)

  const isActiveTab = location.pathname.substring(1)

  return (
    // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
    <StyledMenu ref={node as any}>
      <StyledMenuButton onClick={toggle}>
        <StyledMenuIcon />
      </StyledMenuButton>
      {open && (
        <MenuFlyout>
          <MainNavLinksWrapper>
            <GroupStakingTabs active={isActiveTab} />
          </MainNavLinksWrapper>
          <MenuItem id="link" href="http://unifund.global/">
            <Info size={14} />
            About
          </MenuItem>
          <MenuItem id="link" href="https://medium.com/@unifundGlobal">
            <BookOpen size={14} />
            Medium
          </MenuItem>
          <MenuItem id="link" href="https://twitter.com/UnifundGlobal">
            <Twitter size={14} />
            Twitter
          </MenuItem>
          <MenuItem id="link" href="https://t.me/UnifundGlobal">
            <MessageCircle size={14} />
            Telegram
          </MenuItem>
          <MenuItem id="link" href="https://discord.gg/qeu4suq">
            <MessageCircle size={14} />
            Discord
          </MenuItem>
        </MenuFlyout>
      )}
    </StyledMenu>
  )
}

export default withRouter(Menu)
