import React, { useState } from 'react'
import styled from 'styled-components'
import { useTranslation } from 'react-i18next'
import DefaultLayout from 'src/layouts/default'
import web3 from 'web3'
import { MainHeading } from 'src/components/NavigationTabs'
import { FlexBox } from 'src/components/FlexBox'
import { useGroupActions } from 'src/context/GroupContext'
import SliderPanel from 'src/components/SliderPanel'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import { css } from '@emotion/core'
import { ButtonError } from 'src/components/Button'
import { PageSpinner } from 'src/components/PageSpinner'
import { TYPE } from 'src/theme'

const StyledFlexBox = styled(FlexBox)`
  ${({ theme }) => theme.mediaWidth.upToSmall`
    flex-flow: column;
  `};
`

const BalanceWrapper = styled.div`
  background: ${({ theme }) => theme.bg1};
  border-radius: 0.25rem;
  padding: 0.25rem 0.5rem;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    margin-top: 0.5rem;
  `};
`

const GroupAddFunds = ({
  history,
  match: {
    params: { id }
  }
}: RouteComponentProps<{ id?: string }>) => {
  const { getGroup, addFunds } = useGroupActions()
  const [amount, setAmount] = useState(0)
  const [attempting, setAttempting] = useState(false)
  const { t } = useTranslation()

  const group = getGroup(parseInt(id!))
  // const minValue = group?.params.maxEthContribution || 1
  // const maxValue = group?.params.maxEthAcceptable! - group?.totalContributions! || 1
  const actualMaxAllowed = group?.data
    ? parseFloat(web3.utils.fromWei(group?.data.maxEthAcceptable ?? '0')) -
      parseFloat(web3.utils.fromWei(group?.data.totalContributions ?? '0'))
    : 1
  const realMax = parseFloat(web3.utils.fromWei(group?.data.maxEthContribution ?? '0'))
  const minValue = 0
  const maxValue = actualMaxAllowed < realMax ? actualMaxAllowed : realMax

  return (
    <>
      <MainHeading
        backPath={'/'}
        title={t('Add funds')}
        helperText="If you have any trouble with this form, please contact us on Telegram."
      />
      <DefaultLayout
        isChildPage
        css={css`
          max-width: 800px;
          width: 100%;
        `}
      >
        <FlexBox flex="column" justify="flex-end">
          {group ? (
            <>
              <StyledFlexBox align="baseline" justify="space-between">
                <h1>
                  {group.params.description.length > 15
                    ? `${group.params.description.substr(0, 15)}...`
                    : group.params.description}
                </h1>
                <BalanceWrapper>
                  <TYPE.body>Current balance: {group?.totalContributions} ETH</TYPE.body>
                </BalanceWrapper>
              </StyledFlexBox>
              <FlexBox padding="1rem 0">
                <SliderPanel
                  label={t('Amount')}
                  helpText="Enter the amount of ETH you want to add to the fund."
                  subLabel={`(${maxValue} ETH maximum)`}
                  unit="ETH"
                  value={amount}
                  min={minValue}
                  max={maxValue}
                  step={1}
                  onChange={(value: number) => {
                    setAmount(value)
                  }}
                  onChangeInput={(event: any) => {
                    setAmount(parseFloat(event.target.value))
                  }}
                  id="field-addFunds"
                />
              </FlexBox>
              <FlexBox justify="flex-end">
                <ButtonError
                  disabled={attempting}
                  onClick={async () => {
                    setAttempting(true)
                    await addFunds(group?.id!, amount)
                    setAttempting(false)
                  }}
                  width="120px"
                >
                  {attempting ? 'Adding...' : 'Add Funds'}
                </ButtonError>
              </FlexBox>
            </>
          ) : (
            <PageSpinner />
          )}
        </FlexBox>
      </DefaultLayout>
    </>
  )
}

export default withRouter(GroupAddFunds)
