import { DataPoint } from 'src/components/DataPoint'
import { StatusBadge } from 'src/components/StatusBadge'
// import { PercentReturn } from 'src/components/PercentReturn'
import { FundsInvested } from 'src/components/FundsInvested'
import { TokensTable } from 'src/components/TokensTable'
import { Spinner } from '../../theme/components'
import Circle from '../../assets/images/blue-loader.svg'

export const RowAlignRight = styled(Row)`
  justify-content: flex-end;
`

const StyledFundCardHeadingWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    flex-flow: column;
    align-items: start;
  `};
`

const StyledTitleInfoWrapper = styled.div`
  ${({ theme }) => theme.mediaWidth.upToSmall`
    padding-bottom: 1rem;
  `};
`

const StyledFlexCenterWrapper = styled.div`
  display: flex;
  align-items: center;
`

const StyledH1 = styled.h1`
  margin-right: 0.5rem;
`

const StyledH2 = styled.h2`
  padding-top: 1.5rem;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    padding-top: 1rem;
  `};
`

const StyledDataPointsWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 1.5rem;
`

const StyledSpinnerWrapper = styled.div`
  display: flex;
  justify-content: center;
  padding: 2rem 0;
`

// import { RouteComponentProps } from 'react-router-dom';

interface DetailsLocationState {
  isMyFind?: any
}

// function FliplTimeline(
//   props: RouteComponentProps<{}, any, FliplTimelineLocationState | any>
// )

const GroupDetails = ({
  history,
  match: {
    params: { id }
  }
}: RouteComponentProps<{ id?: string }, any, DetailsLocationState | any>) => {
  const { t } = useTranslation()
  const { getGroup } = useGroupActions()
  // const { account } = useActiveWeb3React()

  const group = getGroup(parseInt(id!))

  const isMyFund = history.location.state?.isMyFund
  // const isLeader = account?.toLowerCase() === group?.data.leader.toLowerCase()
  // const minValue = group?.data ? web3.utils.fromWei(group?.data.maxEthContribution) : 1
  // const maxValue = (group?.data
  //   ? parseFloat(web3.utils.fromWei(group?.data.maxEthAcceptable)) -
  //     parseFloat(web3.utils.fromWei(group?.data.totalContributions))
  //   : 1
  // ).toFixed(2)

  // const groupFull = minValue > maxValue
  // const groupName = group?.additionalInfo?.name ? group.additionalInfo?.name : 'Group ' + parseInt(id!)
  // const groupDescription = group?.additionalInfo?.description ? group.additionalInfo?.description : ''

  // const [isEditingName, setEditingName] = useState(false)
  // const [tempGroupName, setTempGroupName] = useState('')
  // const [isEditingDescription, setEditingDescription] = useState(false)
  // const [tempGroupDescription, setTempGroupDescription] = useState('')

  // const theme = useContext(ThemeContext)
  const started = group?.data.runTimeout > 0
  const status = group?.data.complete ? 'completed' : group?.data.aborted ? 'canceled' : started ? 'active' : 'open'
  const progressPercentage = (group?.data.totalContributions / group?.data.maxEthAcceptable) * 100
  const contributions = group?.data.totalContributions / Math.pow(10, 18)

  console.log('group', group)

  return (
    <>
      <MainHeading
        backPath={isMyFund ? '/' : status === 'open' ? '/open' : '/closed'}
        title={t('Fund details')}
        showShare
      />
      <DefaultLayout
        isChildPage
        css={css`
          max-width: 800px;
          width: 100%;
        `}
      >
        {group ? (
          <>
            {/* {isLeader && false ? (
                <h2>
                  {isEditingName ? (
                    <>
                      <Input
                        className="name-input"
                        type="text"
                        autoComplete="off"
                        autoCorrect="off"
                        autoCapitalize="off"
                        spellCheck="false"
                        placeholder="Eg. No pain no gain"
                        onChange={event => {
                          const name = event.target.value
                          setTempGroupName(name)
                        }}
                        value={tempGroupName || groupName}
                        autoFocus
                        css={css`
                          display: inline-block;
                          width: 60%;
                        `}
                      />
                      <Button
                        onClick={async () => {
                          await updateGroupInfo({
                            groupId: group!.id!,
                            name: tempGroupName || groupName,
                            description: tempGroupDescription || groupDescription
                          })
                          setEditingName(false)
                        }}
                        css={css`
                          display: inline-block;
                          width: 40%;
                        `}
                      >
                        Save
                      </Button>
                    </>
                  ) : (
                    <>
                      <span>
                        {tempGroupName || groupName}{' '}
                        {group?.verified ? <img src={VerifiedBadge} alt="verified" style={{ width: '20px' }} /> : null}
                      </span>
                      <div
                        css={css`
                          padding: 5px 0 0 15px;
                          display: inline-block;
                          vertical-align: middle;
                          filter: invert(1);
                          cursor: pointer;
                        `}
                        onClick={() => {
                          setEditingName(true)
                        }}
                      >
                        <img src={EditIcon} alt="edit" style={{ width: '18px' }} />
                      </div>
                    </>
                  )}
                </h2>
              ) : (
                <h2>
                  {groupName}{' '}
                  {group?.verified ? <img src={VerifiedBadge} alt="verified" style={{ width: '20px' }} /> : null}
                </h2>
              )}

              {isLeader && false ? (
                <h2>
                  {isEditingDescription ? (
                    <>
                      <Textarea
                        className="name-input"
                        autoComplete="off"
                        autoCorrect="off"
                        autoCapitalize="off"
                        spellCheck="false"
                        placeholder="Eg. Follow my twitter: @nopumpnolife"
                        onChange={event => {
                          const description = event.target.value
                          setTempGroupDescription(description)
                        }}
                        value={tempGroupDescription || groupDescription}
                        autoFocus
                        css={css``}
                      />
                      <Button
                        onClick={async () => {
                          await updateGroupInfo({
                            groupId: group!.id!,
                            name: tempGroupName || groupName,
                            description: tempGroupDescription || groupDescription
                          })
                          setEditingDescription(false)
                        }}
                        css={css`
                          margin-top: 10px;
                        `}
                      >
                        Save
                      </Button>
                    </>
                  ) : (
                    <>
                      <Panel
                        onClick={() => {
                          setEditingDescription(true)
                        }}
                      >
                        <div
                          css={css`
                            padding: 10px;
                            font-size: 16px;
                            cursor: pointer;
                          `}
                        >
                          {tempGroupDescription || groupDescription || <em>Click here to set a description</em>}
                        </div>
                      </Panel>
                    </>
                  )}
                </h2>
              ) : (
                <div>{groupDescription}</div>
              )}

              <h3
                css={css`
                  margin-bottom: 5px;
                `}
              >
                Contract info
              </h3>
              <GroupProperties group={group} account={account!} />

              <h3
                css={css`
                  margin-bottom: 5px;
                `}
              >
                Is the group open?
              </h3>
              {groupFull ? <div>Group is full.</div> : <div>Group is open.</div>} */}
            <StyledFundCardHeadingWrapper>
              <StyledTitleInfoWrapper>
                {group.id !== undefined ? (
                  <StyledFlexCenterWrapper>
                    <StyledH1>
                      {group.params.description.length > 15
                        ? `${group.params.description.substr(0, 15)}...`
                        : group.params.description}
                    </StyledH1>
                    <StatusBadge status={status}>{status}</StatusBadge>
                  </StyledFlexCenterWrapper>
                ) : null}
                <TYPE.body fontSize="14px" marginTop="4px">
                  Managed by{' '}
                  <a href={`https://etherscan.io/address/${group.data.leader}`} target="_blank" rel="noreferrer">
                    {group.data.leader.substr(0, 15)}...
                  </a>
                </TYPE.body>
              </StyledTitleInfoWrapper>
              {/* <ButtonPrimary width="190px">Action Buttons Here</ButtonPrimary> */}
            </StyledFundCardHeadingWrapper>
            {status === 'open' || status === 'canceled' ? (
              <>
                <StyledDataPointsWrapper>
                  {/* Number of investors needs to be passed here */}
                  {/* <DataPoint label="Investors" value={'0'} size="large" /> */}
                  {/* Manager's stake needs to be passed here */}
                  {/* <DataPoint
                    label="Manager's stake"
                    value={'0'}
                    helpText="The amount the manager of the fund has invested."
                    unit="ETH"
                    size="large"
                  /> */}
                  <DataPoint
                    label="Joining fee"
                    value={`${numeral(group.params.requiredMemberiFundFee).format('0,0')}`}
                    unit="iFUND"
                    size="large"
                  />
                  <DataPoint
                    label="Collateral"
                    value={`${numeral(group.params.leaderiFundCollateral).format('0,0')}`}
                    helpText="If the fund makes a loss the collateral is used to compensate the investors in the fund relative to their investment size."
                    unit="iFUND"
                    size="large"
                  />
                </StyledDataPointsWrapper>
                <FundsInvested contributions={contributions} progressPercentage={progressPercentage} size="large" />
              </>
            ) : (
              <StyledDataPointsWrapper>
                {/* Number of investors needs to be passed here */}
                {/* <DataPoint label="Balance" value={'10'} unit="ETH" size="large" /> */}
                {/* Manager's stake needs to be passed here */}
                <DataPoint label="Starting balance" value={group.totalContributions} unit="ETH" size="large" />
                {/* <DataPoint label="Return" value={<PercentReturn value={-32} size="large" />} size="large" /> */}
                {/* <DataPoint label="Max time left" value={'70'} unit="days" size="large" /> */}
              </StyledDataPointsWrapper>
            )}
            {group.tokens && group.tokens.length > 0 && (
              <>
                <StyledH2>Tokens held</StyledH2>
                <TokensTable tokens={group.tokens} />
              </>
            )}
            <StyledH2>Details</StyledH2>
            <TYPE.body fontSize="16px" marginTop="0.5rem">
              {group.params.description}
            </TYPE.body>
            <StyledDataPointsWrapper>
              <DataPoint
                label="Manager fee"
                helpText="This fee is deducted from the return the fund makes (if it makes one) and paid to the manager of the fund."
                value={`${group.params.leaderProfitShareOutOf10000 / 100}%`}
                size="small"
              />
              <DataPoint
                label="Starting size"
                helpText="The fund can be started by the manager when the ETH invested is between these values."
                value={`${group.params.minEthToStart} – ${group.params.maxEthAcceptable} ETH`}
                size="small"
              />
              {/* <DataPoint
                label="Max investment"
                value={`${group.params.maxEthContribution} ETH`}
                helpText="The maximum investment size per investor."
                size="small"
              /> */}
              <DataPoint
                label="Max trade time"
                value={`${group.params.maxRunTimeHours / 24} days`}
                helpText="The maximum time the fund can make trades. The manager can close the fund before this time."
                size="small"
              />
            </StyledDataPointsWrapper>
          </>
        ) : (
          <StyledSpinnerWrapper>
            <Spinner src={Circle} />
          </StyledSpinnerWrapper>
        )}
      </DefaultLayout>
    </>
  )
}

export default withRouter(GroupDetails)
