const StyledHeaderWrapper = styled.div`
  padding-bottom: 1.5rem;
`

const StyledSpinnerWrapper = styled.div`
  display: flex;
  justify-content: center;
  padding: 2rem 0;
`

const TokenList = ({ tokens, chainId }: any) => {
  return (
    <AutoColumn>
      {tokens === undefined ? (
        <StyledSpinnerWrapper>
          <Spinner src={Circle} />
        </StyledSpinnerWrapper>
      ) : null}
      {tokens !== undefined && tokens.length === 0 ? <div>No tokens yet</div> : null}
      {tokens?.map((token: any) => (
        <TokenPanel key={token.name} name={token.name} symbol={token.symbol} address={token.address} />
      ))}
    </AutoColumn>
  )
}

