import { Token } from 'src/sdk'
// import { transparentize } from 'polished'
import React, { useContext } from 'react'
import { useActiveWeb3React } from 'src/hooks'
import { Text } from 'rebass'
import { TYPE } from 'src/theme'
// import { ExternalLink } from 'src/theme/components'
import { getEtherscanLink } from 'src/utils'
import styled, { ThemeContext } from 'styled-components'
import { AutoColumn } from '../Column'
import CurrencyLogo from '../CurrencyLogo'

const Wrapper = styled.div`
  border: ${({ theme }) => `1px solid ${theme.bg3}`};
  padding: 1rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  font-size: 1rem;
  margin-bottom: 0.5rem;
`

export default ({ name, decimals = 18, symbol, address, amount }: any) => {
  const { chainId } = useActiveWeb3React()
  const theme = useContext(ThemeContext)
  return (
    <Wrapper>
      <AutoColumn gap="24px">
        <CurrencyLogo currency={new Token(chainId!, address, decimals, symbol, name)} size={'24px'} />
      </AutoColumn>
      <AutoColumn gap="0.25rem" justify="flex-start">
        <TYPE.main color={theme.text1} marginLeft="0.75rem">
          {amount ? amount + ' ' : ''}
          {name && symbol && name !== symbol ? `${symbol} (${name})` : name || symbol}{' '}
        </TYPE.main>
        {chainId && (
          <a href={getEtherscanLink(chainId, address, 'token')}>
            <Text fontSize="14px" marginLeft="0.75rem" title={address}>
              View on Etherscan
            </Text>
          </a>
        )}
      </AutoColumn>
    </Wrapper>
  )
}
