import React, { useContext, useEffect, useState } from 'react'
import numeral from 'numeral'
import { parseEther } from 'ethers/lib/utils'
import { ThemeContext } from 'styled-components'
import { StyledTableRow, StyledTableCell } from 'src/components/TokensTable'
import { useActiveWeb3React, useOldWeb3 } from 'src/hooks'
import * as StakingService from 'src/services/stakingService'
import * as TokenService from 'src/services/tokenService'
import InfoModal from 'src/components/InfoModal'
import SliderPanel from 'src/components/SliderPanel'
import { TYPE } from 'src/theme'
import { ButtonError } from 'src/components/Button'
import { FlexBox } from 'src/components/FlexBox'
import { PageSpinner } from '../PageSpinner'

enum Status {
    None = 0,
    Checking = 1,
    Checked = 2,
    Approving = 3,
    Approved = 4,
    Staking = 5,
    Staked = 6,
    Claiming = 7,
    Claimed = 8,
    Unstaking = 9,
    Unstaked = 10
}

export function PoolStake({
    poolId,
    symbol,
    sliderStep
}: {
    poolId: number,
    symbol: string,
    sliderStep: number
}) {
    const [stakeAmount, setStakeAmount] = useState(0)
    const [stakeAmountValid, setStakeAmountValid] = useState(false)
    const [unstakeAmount, setUnstakeAmount] = useState(0)
    const [unstakeAmountValid, setUnstakeAmountValid] = useState(false)
    const [balance, setBalance] = useState(0)
    const [status, setStatus] = useState(Status.None)
    const [modalOpen, setModalOpen] = useState(false)
    const [isError, setIsError] = useState(false)
    const [isLoading, setIsLoading] = useState(true)
    const [statusMessage, setStatusMessage] = useState("")
    const [stakingState, setStakingState] = useState<any>(undefined)

    const { account } = useActiveWeb3React()
    const web3 = useOldWeb3()
    const theme = useContext(ThemeContext)

    const stake = async (amount: string) => await StakingService.stake(poolId, parseEther(amount), web3, account!)
    const checkStakingAllowance = async (amount: string) => await StakingService.checkStakingAllowance(poolId, parseEther(amount), web3, account!)
    const approveStaking = async (amount: string) => await StakingService.approveStake(poolId, parseEther(amount), web3, account!)
    const claim = async () => await StakingService.claim(poolId, web3, account!)
    const unstake = async (amount: string) => await StakingService.unstake(poolId, parseEther(amount), web3, account!)

    const onDismiss = () => {
        if(!isError) {
            setStakeAmount(0)
            setStakeAmountValid(false)
            setUnstakeAmount(0)
            setUnstakeAmountValid(false)
        }
        setIsError(false)
        setStatusMessage("")
        setModalOpen(false)
    }

    const loadStateAndBalance = async () => {
        if (web3 && account) {
            setIsLoading(true)
            const b = await TokenService.getBalanceByPoolId(poolId, web3, account!)
            setBalance(b)
            const s = await StakingService.getUserStakingState(poolId, web3, account!)
            setStakingState(s)
            setIsLoading(false)
        }
    }

    useEffect(() => {
        loadStateAndBalance()
    }, [account, status, poolId])

    return (
        <>
            <InfoModal isOpen={modalOpen} onDismiss={onDismiss} isError={isError} message={statusMessage} />
            { (!isLoading && balance && parseFloat(balance.toFixed(2)).toFixed(2) !== '0.00') ? (
                <>
                    {stakingState?.totalStaked > 0 ? (
                        <FlexBox flex="column">
                            <FlexBox flex="column" padding="0 0 2.5em 0">
                                <StyledTableRow>
                                    <StyledTableCell width="50%">
                                        <TYPE.body fontSize="0.875em">Staked</TYPE.body>
                                    </StyledTableCell>
                                    <StyledTableCell width="50%">
                                        <TYPE.body color={theme.text1} fontWeight={600}>{`${numeral(stakingState.totalStaked).format('0,0.00')} iFUND`}</TYPE.body>
                                    </StyledTableCell>
                                </StyledTableRow>
                                <StyledTableRow>
                                    <StyledTableCell width="50%">
                                        <TYPE.body fontSize="0.875em">iFUND Rewards</TYPE.body>
                                    </StyledTableCell>
                                    <StyledTableCell width="50%">
                                        <TYPE.body color={theme.text1} fontWeight={600}>{`${numeral(stakingState.iFund).format('0,0.00')} iFUND`}</TYPE.body>
                                    </StyledTableCell>
                                </StyledTableRow>
                                <StyledTableRow>
                                    <StyledTableCell width="50%">
                                        <TYPE.body fontSize="0.875em">iGOV Rewards</TYPE.body>
                                    </StyledTableCell>
                                    <StyledTableCell width="50%">
                                        <TYPE.body color={theme.text1} fontWeight={600}>{`${numeral(stakingState.iGov).format('0,0.00')} iGOV`}</TYPE.body>
                                    </StyledTableCell>
                                </StyledTableRow>
                            </FlexBox>
                            <SliderPanel
                                label="Amount to unstake"
                                subLabel={`(${numeral(stakingState.totalStaked.toFixed(2)).format('0,0.00')} available)`}
                                value={unstakeAmount}
                                unit={symbol}
                                min={0}
                                max={parseFloat(stakingState.totalStaked)}
                                step={sliderStep}
                                onChange={(value: number) => {
                                    setUnstakeAmount(value)
                                    setUnstakeAmountValid(!Number.isNaN(value) && value > 0)
                                }}
                                onChangeInput={(event: any) => {
                                    setUnstakeAmount(event.target.value)
                                    setUnstakeAmountValid(!Number.isNaN(event.target.value) && event.target.value > 0)
                                }}
                                showMaxButton={true}
                                onMax={() => {
                                    setUnstakeAmount(stakingState.totalStaked)
                                    setUnstakeAmountValid(stakingState.totalStaked > 0)
                                }}
                                id="field-stakingAmount"
                            />
                            <>
                                {stakingState && stakingState.penalty 
                                ? <TYPE.subHeader>{`Early unstaking is subject to ${stakingState.penaltyPercent} penalty on staked amount`}</TYPE.subHeader> 
                                : null}
                            </>
                            
                            <FlexBox justify="flex-end" padding="1.5rem 0" width="100%">
                                <ButtonError disabled={!unstakeAmountValid || status === Status.Unstaking}
                                    onClick={async () => {
                                        setStatus(Status.Unstaking)
                                        try {
                                            await unstake(unstakeAmount.toString())
                                            setStatus(Status.Unstaked)
                                            setIsError(false)
                                            setStatusMessage(`${unstakeAmount} ${symbol} unstaked`)
                                        } catch (e) {

                                            setStatus(Status.None)
                                            setIsError(true)
                                            setStatusMessage('Unstaking failed. Details: ' + e.message)
                                        }
                                        setModalOpen(true)
                                    }}
                                    width="150px"
                                >
                                    {status === Status.Unstaking ? 'Unstaking...' : 'Unstake'}
                                </ButtonError>
                                <ButtonError disabled={stakingState.iFund === 0 || status === Status.Unstaking}
                                    onClick={async () => {
                                        setStatus(Status.Claiming)
                                        try {
                                            await claim()
                                            setStatus(Status.Claimed)
                                            setIsError(false)
                                            setStatusMessage(`${stakingState.iFund} iFUND and ${stakingState.iGov} iGOV claimed`)
                                        } catch (e) {
                                            setStatus(Status.None)
                                            setIsError(true)
                                            setStatusMessage('Claiming rewards failed. Details: ' + e.message)
                                        }
                                        setModalOpen(true)
                                    }}
                                    margin="0 0 0 0.75rem"
                                    width="150px"
                                >
                                    {status === Status.Claiming ? 'Claiming...' : 'Claim reward'}
                                </ButtonError>
                            </FlexBox>
                        </FlexBox>
                    ) : null}

                    <SliderPanel
                        label="Amount to stake"
                        subLabel={`(${numeral(parseFloat(balance.toFixed(2)).toFixed(2)).format('0,0.00')} available)`}
                        value={stakeAmount}
                        unit={symbol}
                        min={0}
                        max={parseFloat(balance.toFixed(2))}
                        step={sliderStep}
                        onChange={(value: number) => {
                            setStakeAmount(value)
                            setStakeAmountValid(!Number.isNaN(value) && value > 0)
                        }}
                        onChangeInput={(event: any) => {
                            setStakeAmount(event.target.value)
                            setStakeAmountValid(!Number.isNaN(event.target.value) && event.target.value > 0)
                        }}
                        showMaxButton
                        onMax={() => {
                            setStakeAmount(balance ? parseFloat(balance.toFixed(2)) : 0)
                            setStakeAmountValid(balance > 0)
                        }}
                        id="field-stakingAmount"
                    />

                    <FlexBox padding="1.5rem 0 0" justify="flex-end">
                        {status === Status.None || status === Status.Checking ? (
                            <ButtonError
                                disabled={!stakeAmountValid || status !== Status.None}
                                onClick={async () => {
                                    setStatus(Status.Checking)
                                    if (await checkStakingAllowance(stakeAmount.toString())) {
                                        setStatus(Status.Approved)
                                    } else {
                                        setStatus(Status.Checked)
                                    }
                                }}
                                width="180px"
                            >
                                {status === Status.Checking ? 'Checking...' : 'Check Allowance'}
                            </ButtonError>
                        ) : null}
                        {status !== Status.None && status !== Status.Checking ? (
                            <ButtonError
                                disabled={!stakeAmountValid || status !== Status.Checked}
                                onClick={async () => {
                                    setStatus(Status.Approving)
                                    try {
                                        await approveStaking(stakeAmount.toString())
                                        setStatus(Status.Approved)
                                    } catch (e) {
                                        setStatus(Status.Checked)
                                        setIsError(true)
                                        setStatusMessage(`Approving the stake amount failed. Please check the form and try again. Details: ${e.message}`)
                                        setModalOpen(true)
                                    }
                                }}
                                width="150px"
                            >
                                {status === Status.Approving ? 'Approving...' : status === Status.Checked ? `Approve ${symbol}` : 'Approved'}
                            </ButtonError>
                        ) : null}
                        <ButtonError
                            disabled={status !== Status.Approved || !stakeAmountValid}
                            onClick={async () => {
                                setStatus(Status.Staking)
                                try {
                                    await stake(stakeAmount.toString())
                                    setStatus(Status.Staked)
                                    setIsError(false)
                                    setStatusMessage(`${stakeAmount} ${symbol} staked`)
                                } catch (e) {
                                    setStatus(Status.Approved)
                                    setIsError(true)
                                    setStatusMessage(`Staking failed. Please check the form and try again. Details: ${e.message}`)
                                }
                                setModalOpen(true)
                            }}
                            width="150px"
                            margin="0 0 0 0.75rem"
                        >
                            {status === Status.Staking ? 'Staking...' : status === Status.Staked ? 'Staked' : 'Stake'}
                        </ButtonError>
                    </FlexBox>
                </>
            ) : <PageSpinner/>}</>)
}