import UnifundStaking from '../constants/unifund/UnifundStaking.abi.json'
import MasterChef from '../constants/unifund/UnifundMasterChef.abi.json'
import ERC20Abi from '../constants/abis/erc20.json'
import { BigNumber } from '@ethersproject/bignumber'
import { formatEther } from 'ethers/lib/utils'

export const checkStakingAllowance = async (poolId: number, amount: BigNumber, web3: any, account: string) => {
  const tokenAddress = poolId === 0 ? process.env.REACT_APP_UNIFUND_ADDRESS! : process.env.REACT_APP_LP_ADDRESS!
  const tokenContract = new web3.eth.Contract(ERC20Abi, tokenAddress)
  const allowance = BigNumber.from(
    (
      await tokenContract.methods
        .allowance(account, process.env.REACT_APP_MASTER_CHEF_ADDRESS)
        .call({ from: account })
    ).toString()
  )

  return allowance.eq(amount)
}

export const approveStake = async (poolId: number, amount: BigNumber, web3: any, account: string) => {
  const tokenAddress = poolId === 0 ? process.env.REACT_APP_UNIFUND_ADDRESS! : process.env.REACT_APP_LP_ADDRESS!
  const tokenContract = new web3.eth.Contract(ERC20Abi,tokenAddress)

  const allowance = BigNumber.from(
    (
      await tokenContract.methods
        .allowance(account, process.env.REACT_APP_MASTER_CHEF_ADDRESS)
        .call({ from: account })
    ).toString()
  )
  if (!allowance.eq(amount)) {
    await tokenContract.methods
      .approve(process.env.REACT_APP_MASTER_CHEF_ADDRESS, amount.toString())
      .send({ from: account })
  }
}

export const stake = async (poolId: number, amount: BigNumber, web3: any, account: string) => {
  const masterChefContract = new web3.eth.Contract(MasterChef, process.env.REACT_APP_MASTER_CHEF_ADDRESS!)
  await masterChefContract.methods.deposit(poolId, amount.toString()).send({ from: account })
}

export const unstakePhase1 = async (web3: any, account: string) => {
  const stakingContract = new web3.eth.Contract(UnifundStaking, process.env.REACT_APP_UNIFUND_STAKING_ADDRESS!)
  await stakingContract.methods.exit().send({ from: account })
}

export const unstake = async (poolId: number, amount: BigNumber, web3: any, account: string) => {
  const masterChefContract = new web3.eth.Contract(MasterChef, process.env.REACT_APP_MASTER_CHEF_ADDRESS!)
  await masterChefContract.methods.withdraw(poolId, amount.toString()).send({ from: account })
}

export const claim = async (poolId: number, web3: any,account: string) => {
  const masterChefContract = new web3.eth.Contract(MasterChef, process.env.REACT_APP_MASTER_CHEF_ADDRESS!)
  await masterChefContract.methods.withdraw(poolId, 0).send({ from: account })
}

export const getUserStakingState = async (poolId: number, web3: any, account: string) => {
  const masterChefContract = new web3.eth.Contract(MasterChef, process.env.REACT_APP_MASTER_CHEF_ADDRESS!)
  const pendingIGovTokens = formatEther(await masterChefContract.methods.pendingiGov(poolId, account).call({ from: account }))
  const unifundMultiplier: number = await masterChefContract.methods.unifundMultiplier().call({ from: account })
  const pendingUnifundTokens = parseFloat(pendingIGovTokens) * unifundMultiplier
  const userInfo = await masterChefContract.methods.userInfo(poolId, account).call({ from: account })
 
  const totalStaked = parseFloat(formatEther(userInfo.amount))
  const penalty = await masterChefContract.methods.penaltyActive(poolId, account).call({ from: account })
  const penaltyPercent = await masterChefContract.methods.penaltyPercent().call({ from: account })
  console.log(penalty)
  return {
    iGov: pendingIGovTokens,
    iFund: pendingUnifundTokens,
    totalStaked: totalStaked,
    penalty: penalty,
    penaltyPercent: penaltyPercent
  }
}

export const getGlobalStakingState = async(web3: any, account: string) =>{
  const masterChefContract = new web3.eth.Contract(MasterChef, process.env.REACT_APP_MASTER_CHEF_ADDRESS!)
  const unifundContract = new web3.eth.Contract(ERC20Abi, process.env.REACT_APP_UNIFUND_ADDRESS!)

  const iGovPerBlock = parseFloat(formatEther(await masterChefContract.methods.iGovPerBlock().call({ from: account })))
  const unifundMultiplier: number = await masterChefContract.methods.unifundMultiplier().call({ from: account })
  const totalRewards = parseFloat(formatEther(await masterChefContract.methods.UnifundRewardBalance().call({ from: account })))
  const totalSupply =  parseFloat(formatEther(await unifundContract.methods.balanceOf(process.env.REACT_APP_MASTER_CHEF_ADDRESS).call({ from: account })))
  const totalStaked = totalSupply - totalRewards
  const totalStakedPercent = (totalStaked / totalSupply)*100
  const bloksPerMonth = 4 * 60 * 24 * 30
  const iGovRewardsPerMonth = bloksPerMonth * iGovPerBlock
  const iFundRewardsPerMonth = bloksPerMonth * iGovPerBlock * unifundMultiplier

  return {
    totalRewards: totalRewards,
    totalSupply: totalSupply,
    totalStaked: totalStaked,
    totalStakedPercent: totalStakedPercent,
    iGovRewardsPerMonth: iGovRewardsPerMonth,
    iFundRewardsPerMonth: iFundRewardsPerMonth
  }
}

