import UnifundGroupAbi from '../constants/unifund/UnifundGroup.abi.json'
import UnifundGroupHelperLibraryAbi from '../constants/unifund/UnifundGroupHelperLibrary.abi.json'
import ERC20Abi from '../constants/abis/erc20.json'
import { BigNumber } from '@ethersproject/bignumber'

const ZeroAddress = '0x0000000000000000000000000000000000000000'

export const getMyGroups = async (web3: any, account: string) => {
  const groupContract = new web3.eth.Contract(UnifundGroupAbi, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!)
  const groupHelperContract = new web3.eth.Contract(
    UnifundGroupHelperLibraryAbi,
    process.env.REACT_APP_UNIFUND_GROUP_LIBRARY_ADDRESS!
  )

  const groupCount = await groupContract.methods.groupCount().call({ from: account })
  let myGroupIds: any = []

  // member
  let result = await groupHelperContract.methods.memberOfGroupIds(account, 0).call({ from: account })
  myGroupIds = myGroupIds.concat(result.groupIds)
  while (result.nextGroupId < groupCount) {
    result = await groupHelperContract.methods.memberOfGroupIds(account, result?.nextGroupId).call({ from: account })
    myGroupIds = myGroupIds.concat(result.groupIds)
  }

  // leader
  const leaderGroupsCount = await groupContract.methods.groupIdsByLeaderCount(account).call({ from: account })
  let groupIds = await groupHelperContract.methods.leaderOfGroupIds(account, 0).call({ from: account })
  while (groupIds.length < leaderGroupsCount) {
    groupIds = groupIds.concat(
      await groupHelperContract.methods.leaderOfGroupIds(account, groupIds.length).call({ from: account })
    )
  }
  const myGroupIdsSet = new Set(myGroupIds.concat(groupIds))
  myGroupIds = [...myGroupIdsSet]

  const myGroups: any = []
  myGroupIds
    .sort()
    .reverse()
    .forEach(async (groupId: number) => {
      const group = await groupContract.methods.group(groupId).call({ from: account })
      const myContribution = await groupContract.methods.groupContribution(groupId, account).call({ from: account })
      const address = group.aborted ? ZeroAddress : process.env.REACT_APP_WETH_ADDRESS

      const withdrawn =
        group.aborted || group.complete
          ? await groupContract.methods.groupMemberWithdrawal(groupId, account, address).call({ from: account })
          : false

      myGroups.push({
        group: group,
        groupId: groupId.toString(),
        myContribution: myContribution,
        withdrawn: withdrawn
      })
    })

  return myGroups
}

export const getOpenGroups = async (web3: any, account: string) => {
  const groupContract = new web3.eth.Contract(UnifundGroupAbi, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!)
  const groupHelperContract = new web3.eth.Contract(
    UnifundGroupHelperLibraryAbi,
    process.env.REACT_APP_UNIFUND_GROUP_LIBRARY_ADDRESS!
  )
  const openGroupsCount = await groupContract.methods.groupIdsBeingStartedCount().call({ from: account })
  const openGroups: any = []

  if (openGroupsCount === 0) {
    return openGroups
  }

  let groupIds = await groupHelperContract.methods.groupIdsBeingStarted(0).call({ from: account })
  while (groupIds.length < openGroupsCount) {
    groupIds = groupIds.concat(
      await groupHelperContract.methods.groupIdsBeingStarted(account, groupIds.length).call({ from: account })
    )
  }
  const set = new Set(groupIds)
  const openGroupIds = [...set]

  openGroupIds
    .sort()
    .reverse()
    .forEach(async (groupId: any) => {
      const group = await groupContract.methods.group(groupId).call({ from: account })
      openGroups.push({ 
        group: group, 
        groupId: groupId.toString(), 
        myContribution: 0, 
        withdrawn: false,
        isGroupManager: await groupContract.methods.authorizedTrader(groupId, account).call({ from: account })
       })
    })

  return openGroups
}

export const getRunningGroups = async (web3: any, account: string) => {
  const groupContract = new web3.eth.Contract(UnifundGroupAbi, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!)
  const groupHelperContract = new web3.eth.Contract(
    UnifundGroupHelperLibraryAbi,
    process.env.REACT_APP_UNIFUND_GROUP_LIBRARY_ADDRESS!
  )
  const runningGroupsCount = await groupContract.methods.groupIdsRunningCount().call({ from: account })
  const runningGroups: any = []

  if (runningGroupsCount === 0) {
    return runningGroups
  }

  let groupIds = await groupHelperContract.methods.groupIdsRunning(0).call({ from: account })
  while (groupIds.length < runningGroupsCount) {
    groupIds = groupIds.concat(
      await groupHelperContract.methods.groupIdsRunning(account, groupIds.length).call({ from: account })
    )
  }
  const set: Set<number> = new Set(groupIds)
  const runningGroupIds: number[] = [...set]

  runningGroupIds
    .sort()
    .reverse()
    .forEach(async (groupId: number) => {
      const group = await groupContract.methods.group(groupId).call({ from: account })
      runningGroups.push({
        group: group,
        groupId: groupId.toString(),
        myContribution: 0,
        withdrawn: false,
        isGroupManager: await groupContract.methods.authorizedTrader(groupId, account).call({ from: account })
      })
    })

  return runningGroups
}

export const getGroups = async (web3: any, account: string) => {
  const groupContract = new web3.eth.Contract(UnifundGroupAbi, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!)

  const groupCount = await groupContract.methods.groupCount().call({ from: account })
  const groups = []

  for (let i = groupCount - 1; i >= 0; i--) {
    const groupId = i.toString()
    const group = await groupContract.methods.group(i).call({ from: account })
    const myContribution = await groupContract.methods.groupContribution(i, account).call({ from: account })
    let withdrawn = false
    const isLeader = account.toLowerCase() === group.leader.toLowerCase()
    if ((group.aborted || group.complete) && (myContribution > 0 || isLeader)) {
      const address = group.aborted ? ZeroAddress : process.env.REACT_APP_WETH_ADDRESS

      withdrawn =
        group.aborted || group.complete
          ? await groupContract.methods.groupMemberWithdrawal(groupId, account, address).call({ from: account })
          : false
    }

    let isGroupManager = false
    if (!group.aborted && !group.complete){
      isGroupManager = await groupContract.methods.authorizedTrader(groupId, account).call({ from: account })
    }    

    groups.push({
      group,
      groupId,
      myContribution,
      withdrawn,
      isGroupManager
    })
  }
  return groups
}

export const getGroup = async (groupId: number, web3: any, account: string) => {
  const groupContract = new web3.eth.Contract(UnifundGroupAbi, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!)
  const group = await groupContract.methods.group(groupId).call({ from: account })
  const myContribution = await groupContract.methods.groupContribution(groupId, account).call({ from: account })
  let withdrawn = false
  const isLeader = account.toLowerCase() === group.leader.toLowerCase()
  if ((group.aborted || group.complete) && (myContribution > 0 || isLeader)) {
    const address = group.aborted ? ZeroAddress : process.env.REACT_APP_WETH_ADDRESS
    withdrawn = await groupContract.methods.groupMemberWithdrawal(groupId, account, address).call({ from: account })
  }

  let isGroupManager = false
  if (!group.aborted && !group.complete){
    isGroupManager = await groupContract.methods.authorizedTrader(groupId, account).call({ from: account })
  }    

  return {
    group: group,
    groupId: groupId,
    myContribution: myContribution,
    withdrawn: withdrawn,
    isGroupManager: isGroupManager
  }
}

export const checkGroupAllowance = async (leaderiFundCollateral: BigNumber, web3: any, account: string) => {
  const unifundContract = new web3.eth.Contract(ERC20Abi, process.env.REACT_APP_UNIFUND_ADDRESS!)

  const allowance = new web3.utils.BN(
    (
      await unifundContract.methods
        .allowance(account, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS)
        .call({ from: account })
    ).toString()
  )
  if (allowance.lt(leaderiFundCollateral)) {
    return false
  }
  return true
}

export const approveGroup = async (leaderiFundCollateral: BigNumber, web3: any, account: string) => {
  const unifundContract = new web3.eth.Contract(ERC20Abi, process.env.REACT_APP_UNIFUND_ADDRESS!)

  const allowance = new web3.utils.BN(
    (
      await unifundContract.methods
        .allowance(account, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS)
        .call({ from: account })
    ).toString()
  )
  if (allowance.lt(leaderiFundCollateral)) {
    await unifundContract.methods
      .approve(process.env.REACT_APP_UNIFUND_GROUP_ADDRESS, leaderiFundCollateral)
      .send({ from: account })
  }
}

export const createGroup = async (
  tokenListId: number,
  maxRuntime: number,
  startTimeout: number,
  leaderProfitPercentage: number,
  leaderiFundCollateral: number,
  memberFee: number,
  maxEthContributionGroup: number,
  minEthToStart: number,
  maxEth: number,
  description: string,
  web3: any,
  account: string
) => {
  const unifundContract = new web3.eth.Contract(ERC20Abi, process.env.REACT_APP_UNIFUND_ADDRESS!)
  const groupContract = new web3.eth.Contract(UnifundGroupAbi, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!)

  const allowance = new web3.utils.BN(
    (
      await unifundContract.methods
        .allowance(account, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS)
        .call({ from: account })
    ).toString()
  )
  if (allowance.lt(leaderiFundCollateral)) {
    return
  }
  await groupContract.methods
    .create(
      tokenListId,
      maxRuntime,
      startTimeout,
      leaderProfitPercentage,
      leaderiFundCollateral,
      memberFee,
      maxEthContributionGroup,
      minEthToStart,
      maxEth,
      description
    )
    .send({ from: account })
}

