export const joinGroup = async (
  groupId: number,
  value: number,
  fee: BigNumber,
  isLeader: boolean,
  web3: any,
  account: string
) => {
  const groupContract = new web3.eth.Contract(UnifundGroupAbi, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!)
  if (!isLeader) {
    const unifundContract = new web3.eth.Contract(ERC20Abi, process.env.REACT_APP_UNIFUND_ADDRESS!)
    const allowance = new web3.utils.BN(
      (
        await unifundContract.methods
          .allowance(account, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS)
          .call({ from: account })
      ).toString()
    )
    if (allowance.lt(fee)) {
      return
    }
  }

  await groupContract.methods
    .join(groupId)
    .send({ from: account, value: new web3.utils.BN(web3.utils.toWei(value.toString())) })
}

export const approveJoinGroup = async (
  groupId: number,
  fee: BigNumber,
  isLeader: boolean,
  web3: any,
  account: string
) => {
  if (isLeader) {
    return
  }
  fee = new web3.utils.BN(fee.toString())
  const unifundContract = new web3.eth.Contract(ERC20Abi, process.env.REACT_APP_UNIFUND_ADDRESS!)
  const allowance = new web3.utils.BN(
    (
      await unifundContract.methods
        .allowance(account, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS)
        .call({ from: account })
    ).toString()
  )
  if (allowance.lt(fee)) {
    await unifundContract.methods.approve(process.env.REACT_APP_UNIFUND_GROUP_ADDRESS, fee).send({ from: account })
  }
}

export const withdraw = async (groupId: number, aborted: boolean, web3: any, account: string) => {
  const groupContract = new web3.eth.Contract(UnifundGroupAbi, process.env.REACT_APP_UNIFUND_GROUP_ADDRESS!)

  if (aborted) {
    await groupContract.methods.withdraw(groupId, process.env.REACT_APP_WETH_ADDRESS).send({ from: account })
    return
  }

  const groupHelperContract = new web3.eth.Contract(
    UnifundGroupHelperLibraryAbi,
    process.env.REACT_APP_UNIFUND_GROUP_LIBRARY_ADDRESS!
  )
  const tokensWithBalances = await groupHelperContract.methods.tokensWithBalances(groupId).call({ from: account })
  const uniqueTokensWithBalances = new Set()
  tokensWithBalances.forEach((address: string) => {
    uniqueTokensWithBalances.add(address.toLowerCase())
  })
  uniqueTokensWithBalances.add(process.env.REACT_APP_UNIFUND_ADDRESS?.toLowerCase())
  uniqueTokensWithBalances.add(process.env.REACT_APP_WETH_ADDRESS?.toLowerCase())

  await groupContract.methods.withdrawMany(groupId, [...uniqueTokensWithBalances]).send({ from: account })
}

