// Slow method
export const getTokenList = async (tokenListId: number, web3: any, account: string) => {
  const tokens = []
  const tokenCount = await tokenListTokenCount(web3.utils.toBN(tokenListId), web3, account!)

  for (let i = 0; i < tokenCount; i++) {
    try {
      const tokenAddress = await tokenListToken(web3.utils.toBN(tokenListId), i, web3, account!)
      const tokenName = await getTokenName(tokenAddress, web3, account!)
      const tokenSymbol = await getTokenSymbol(tokenAddress, web3, account!)
      tokens.push({
        name: tokenName,
        address: tokenAddress,
        symbol: tokenSymbol
      })
    } catch (e) {
      console.debug(e)
    }
  }
  return tokens
}
