import React, { useState } from 'react'
import DefaultLayout from 'src/layouts/default'
import { MainHeading } from 'src/components/NavigationTabs'
import { AutoColumn } from 'src/components/Column'
import { useGroupActions } from 'src/context/GroupContext'
import AddressInputPanel from 'src/components/AddressInputPanel'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import { RowBetween, AutoRow } from 'src/components/Row'
import { css } from '@emotion/core'
import { ButtonError } from 'src/components/Button'
import { isAddress, shortenAddress } from 'src/utils'
import Loader from 'src/components/Loader'
import InfoModal from 'src/components/InfoModal'

const GroupRemoveManager = ({
  history,
  match: {
    params: { id }
  }
}: RouteComponentProps<{ id?: string }>) => {
  const { getGroup, isGroupManager, removeGroupManager } = useGroupActions()
  const [manager, setManager] = useState('')
  const [attempting, setAttempting] = useState(false)
  const [isOpen, setIsOpen] = useState(false)
  const [isError, setIsError] = useState(false)
  const [statusMessage, setStatusMessage] = useState('')
  const group = getGroup(parseInt(id!))

  const onRemoveManager = async () => {
    setAttempting(true)
    try {
      const result = await isGroupManager(group!.id!, manager)
      if (!result) {
        setStatusMessage(`Address ${shortenAddress(manager)} is not a manager for this group`)
      }
      else {
        await removeGroupManager(group?.id!, manager)
        setStatusMessage("Manager removed")
      }
      setIsError(false)
    }
    catch (e) {
      setStatusMessage(e.message ?? e.reason ?? "Error occurred")
      setIsError(true)
    }
    setIsOpen(true)
    setAttempting(false)
  }

  return (
    <DefaultLayout>
      <InfoModal isOpen={isOpen} onDismiss={() => setIsOpen(false)} isError={isError} message={statusMessage} />
      <MainHeading backPath="/" title="Remove Manager" />      
      <AutoColumn
        gap="md"
        css={css`
          text-align: center;
        `}
      >
        {group ? (
          <>
            <AddressInputPanel
              label="Address"
              placeholder=""
              showEtherscanLink={false}
              value={manager}
              onChange={setManager}
              id="field-addManager"
            />

            <RowBetween>
              <div
                css={css`
                  width: 100%;
                  padding: 5px;
                  margin: 0 -5px 0 0;
                `}
              >
                <ButtonError disabled={attempting || !isAddress(manager)} onClick={() => onRemoveManager()}>
                  {
                    attempting
                      ? (
                        <AutoRow gap="0.5em" justify="center">
                          Removing...
                          <Loader stroke="white" />
                        </AutoRow>
                      )
                      : "Remove Manager"
                  }
                </ButtonError>
              </div>
            </RowBetween>
          </>
        ) : (
            <div
              css={css`
              padding: 10px;
            `}
            >
              Group is not found
            </div>
          )}
      </AutoColumn>
    </DefaultLayout>
  )
}

export default withRouter(GroupRemoveManager)
