import React, { useState } from 'react'
import DefaultLayout from 'src/layouts/default'
import { MainHeading } from 'src/components/NavigationTabs'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import { useGroupActions } from 'src/context/GroupContext'
import { css } from '@emotion/core'
import { ButtonError } from 'src/components/Button'
import { PageSpinner } from 'src/components/PageSpinner'
import { ErrorCard } from 'src/components/Card'
import { TYPE } from 'src/theme'

const GroupAbort = ({
  history,
  match: {
    params: { id }
  }
}: RouteComponentProps<{ id?: string }>) => {
  const { getGroup, abortGroup } = useGroupActions()
  const [attempting, setAttempting] = useState(false)

  const group = getGroup(parseInt(id!))
  const started = group?.data.runTimeout > 0
  const status = group?.data.complete ? 'complete' : group?.data.aborted ? 'aborted' : started ? 'started' : 'created'

  const timeOuted = new Date().getTime() > group?.data.runTimeout

  const confirm = async () => {
    setAttempting(true)
    await abortGroup(group?.id!)
    setAttempting(false)
  }

  return (
    <>
      <MainHeading backPath={'/'} title={'Abort fund'} />
      <DefaultLayout
        isChildPage
        css={css`
          max-width: 800px;
          width: 100%;
        `}
      >
        {group ? (
          <>
            {status === 'started' ? (
              timeOuted ? (
                <ErrorCard>Fund has started. End fund before aborting.</ErrorCard>
              ) : (
                <>
                  <h1>Are you sure?</h1>
                  <TYPE.body margin="1rem 0">There is no going back.</TYPE.body>
                  <ButtonError disabled={attempting} onClick={confirm} width="120px">
                    {attempting ? 'Aborting...' : 'Abort fund'}
                  </ButtonError>
                </>
              )
            ) : null}
            {status === 'complete' ? <ErrorCard>Fund has ended.</ErrorCard> : null}
            {status === 'aborted' ? <ErrorCard>Fund already aborted.</ErrorCard> : null}
            {status === 'created' ? (
              <>
                <h1>Are you sure?</h1>
                <TYPE.body margin="1rem 0">There is no going back.</TYPE.body>
                <ButtonError disabled={attempting} onClick={confirm} width="120px">
                  {attempting ? 'Aborting...' : 'Abort fund'}
                </ButtonError>
              </>
            ) : null}
          </>
        ) : (
          <PageSpinner />
        )}
      </DefaultLayout>
    </>
  )
}

export default withRouter(GroupAbort)
