import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
import styled, { css, ThemeContext } from 'styled-components'
import { NavLink } from 'react-router-dom'
import numeral from 'numeral'
import { isGroupLeader, isGroupMember } from 'src/services/accountService'
import { darken } from 'polished'
import { Text } from 'rebass'
import { TYPE } from '../../theme'
import { ChevronRight } from 'react-feather'
import { DataPoint } from '../DataPoint'
import { StatusBadge } from '../StatusBadge'
// import { PercentReturn } from '../PercentReturn'
import { FundsInvested } from '../FundsInvested'

const StyledFundCardWrapper = styled.div`
  border: ${({ theme }) => `1px solid ${theme.bg3}`};
  border-radius: 1rem;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.5s ease;

  :hover {
    border: ${({ theme }) => `1px solid ${theme.primary1}`};
  }

  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    position: relative;
  `};
`

const StyledFundCardHeadingWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`

const StyledFlexCenterWrapper = styled.div`
  display: flex;
  align-items: center;
`

const StyledH2 = styled.h2`
  margin-right: 0.5rem;
`

const StyledDataPointsWrapper = styled.div`
  border-top: ${({ theme }) => `1px solid ${theme.bg3}`};
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 1rem;
  margin-top: 1rem;
`

const StyledChevronRight = styled(ChevronRight)`
  margin-left: 0.5rem;
`

const StatusBadgeWrapper = styled.div`
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
  `};
`

const StyledText = styled(Text)`
  line-height: 1.5;
  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    width: 100px;
  `};
`

const listStyles = css`
  display: grid;
  grid-gap: 0.75rem;
`

const listItemPropertiesStyles = css`
  display: grid;
  grid-gap: 0.25em;
  font-size: 0.875em;
  font-family: monospace;
  text-align: left;
`

const listItemPropertyStyles = css`
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 0.5em;
  overflow: hidden;
  text-overflow: ellipsis;
`

const listItemCaptionStyles = css`
  width: 11em;
`

const listItemActionStyles = css`
  display: flex;
`

const listItemButtonStyles = (theme: any) => css`
  display: flex;
  align-items: center;
  font-size: 1rem;
  font-weight: 600;
  background: ${theme.primary1};
  // background: ${`linear-gradient(135deg, ${theme.primary1} 0%,${theme.primary2} 100%)`};
  border-radius: 0.25em;
  color: ${theme.white};
  cursor: pointer;
  border: none;
  height: 2.5rem;
  outline: none;
  text-decoration: none;
  padding: 0 1rem;
  margin: 1rem 0.5rem 0 0;

  &:hover {
    background: ${darken(0.05, theme.primary1)};
    // background: ${`linear-gradient(45deg, ${theme.primary2} 0%,${theme.primary1} 100%)`};
    color: ${theme.white};
  }
`

export const GroupProperties = ({ group, account }: { group: any; account: string }) => {
  const isMember = isGroupMember(group)
  const isLeader = isGroupLeader(group, account)
  const started = group.data.runTimeout > 0
  const status = group.data.complete ? 'completed' : group.data.aborted ? 'aborted' : started ? 'active' : 'open'
  //const startedDays = (group.data.runTimeout / 60 / 60 - group.params.maxRunTimeHours) / 24
  return (
    <div css={listItemPropertiesStyles}>
      {group.id !== undefined ? (
        <div css={listItemPropertyStyles}>
          <span css={listItemCaptionStyles}>ID</span>
          <span>{group.id}</span>
        </div>
      ) : null}
      {group.params.description ? (
        <div css={listItemPropertyStyles}>
          <span css={listItemCaptionStyles}>Description</span>
          <span>{group.params.description}</span>
        </div>
      ) : null}
      {group.status !== 'openForm' ? (
        <div css={listItemPropertyStyles}>
          <span css={listItemCaptionStyles}>Status</span>
          <span>{status}</span>
        </div>
      ) : null}
      <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Leader</span>
        <span>{group.data.leader}</span>
      </div>
      <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Collateral</span>
        <span>{group.params.leaderiFundCollateral} iFUND</span>
      </div>
      <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Leader profit %</span>
        <span>{group.params.leaderProfitShareOutOf10000 / 100}%</span>
      </div>
      <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Min ETH to Start</span>
        <span>{group.params.minEthToStart} ETH</span>
      </div>
      <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Max ETH Acceptable</span>
        <span>{group.params.maxEthAcceptable} ETH</span>
      </div>
      <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Max ETH per Member</span>
        <span>{group.params.maxEthContribution} ETH</span>
      </div>
      <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Member Fee</span>
        <span>{group.params.requiredMemberiFundFee} iFUND</span>
      </div>
      {/* <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Start Timeout</span>
        <span>{group.params.startTimeoutHours / 24} days</span>
      </div> */}
      <div css={listItemPropertyStyles}>
        <span css={listItemCaptionStyles}>Max Runtime</span>
        <span>{group.params.maxRunTimeHours / 24} days</span>
      </div>
      {/* {group.data.runTimeout ? (
        <div css={listItemPropertyStyles}>
          <span css={listItemCaptionStyles}>Started</span>
          <span>{startedDays > 0 ? `In ${Math.abs(startedDays)} days` : `${Math.abs(startedDays)} days ago`}</span>
        </div>
      ) : null} */}
      {group.totalContributions ? (
        <div css={listItemPropertyStyles}>
          <span css={listItemCaptionStyles}>Total Contribution</span>
          <span>{group.totalContributions} ETH</span>
        </div>
      ) : null}
      {group.myContribution && (isMember || isLeader) ? (
        <div css={listItemPropertyStyles}>
          <span css={listItemCaptionStyles}>My Contribution</span>
          <span>{group.myContribution} ETH</span>
        </div>
      ) : null}
      {group.data.tokenListId !== undefined ? (
        <div css={listItemPropertyStyles}>
          <span css={listItemCaptionStyles}>Token List (ID {group.params.tokenListId})</span>
          {group.tokens === undefined ? <span>Loading...</span> : null}
          {group.tokens !== undefined && group.tokens.length === 0 ? <span>No tokens yet</span> : null}
          {group.tokens !== undefined && group.tokens.length > 0 ? (
            <span>
              {group.tokens?.map((token: any) => {
                return (
                  <>
                    <a
                      css={css`
                        color: white;
                      `}
                      href={`https://etherscan.io/address/${token.address}`}
                    >
                      {token.symbol}
                    </a>
                    : {token.amount.toFixed(2)}
                    <br />
                  </>
                )
              })}
            </span>
          ) : null}
        </div>
      ) : null}
    </div>
  )
}

export const GroupsPanel = ({
  title,
  account,
  groups,
  history,
  pathName
}: {
  title?: string
  account: string
  groups: any
  history: any
  pathName: string
}) => {
  const { t } = useTranslation()
  const theme = useContext(ThemeContext)

  return (
    <div>
      <TYPE.body marginBottom="24px">{t(title)}</TYPE.body>
      <div css={listStyles}>
        {groups.map((group: any) => {
          const started = group.data.runTimeout > 0
          const status = group.data.complete
            ? 'completed'
            : group.data.aborted
            ? 'canceled'
            : started
            ? 'active'
            : 'open'
          const isMember = isGroupMember(group)
          const isLeader = isGroupLeader(group, account)
          const timeOuted = new Date().getTime() > group.runTimeout
          const isMyFund = isMember || isLeader
          console.log(isMyFund)
          const progressPercentage = (group.totalContributions / group.params.maxEthAcceptable) * 100

          let showWithdrawButton = false
          let showSwapButton = false
          let showFinishButton = false
          let showAbortButton = false
          let showStartButton = false
          let showAddFundsButton = false
          let showJoinButton = false

          if (group.data.complete || group.data.aborted) {
            if ((isMember || isLeader) && !group.withdrawn) {
              showWithdrawButton = true
            }
          } else if (started) {
            if (isLeader) {
              showSwapButton = true
              showFinishButton = true
            } else if (isMember && timeOuted) {
              showAbortButton = true
            }
          } else {
            if (isLeader) {
              showStartButton = true
              showAbortButton = true
            }

            if (group.totalContributions < group.params.maxEthAcceptable) {
              if (isMember || isLeader) {
                showAddFundsButton = true
              } else {
                showJoinButton = true
              }
            }
          }

          // this.props.history.push({
          //   pathname: '/tickets',
          //   state: {
          //     data: rowIndex[0],
          //   },
          // })

          return (
            <StyledFundCardWrapper
              key={group.id}
              onClick={() => history.push({ pathname: `/group/${group.id}/details`, state: { isMyFund: isMyFund } })}
            >
              <StyledFundCardHeadingWrapper>
                <div>
                  {group.id !== undefined ? (
                    <StyledFlexCenterWrapper>
                      <StyledH2>
                        {group.params.description.length > 15
                          ? `${group.params.description.substr(0, 15)}...`
                          : group.params.description}
                      </StyledH2>
                      <StatusBadgeWrapper>
                        <StatusBadge status={status}>{status}</StatusBadge>
                      </StatusBadgeWrapper>
                    </StyledFlexCenterWrapper>
                  ) : null}
                  <StyledText fontSize="14px" marginTop="4px">
                    Managed by {group.data.leader.substr(0, 15)}...
                  </StyledText>
                </div>
                <StyledFlexCenterWrapper>
                  {/* Percent return of fund needs to be passed to <PercentReturn /> below */}
                  {/* {(status === 'active' || status === 'completed') && <PercentReturn value={32} size="small" />} */}
                  <StyledChevronRight color={theme.primary1} />
                </StyledFlexCenterWrapper>
              </StyledFundCardHeadingWrapper>
              {status !== 'active' && (
                <StyledDataPointsWrapper>
                  {/* Number of investors needs to be passed here */}
                  {/* <DataPoint label="Investors" value={'0'} size="small" /> */}
                  {/* Manager's stake needs to be passed here */}
                  {/* <DataPoint label="Manager's stake" value={'0 ETH'} size="small" /> */}
                  <DataPoint
                    label="Joining fee"
                    value={`${numeral(group.params.requiredMemberiFundFee).format('0,0')} iFUND`}
                    size="small"
                  />
                  <DataPoint
                    label="Collateral"
                    value={`${numeral(group.params.leaderiFundCollateral).format('0,0')} iFUND`}
                    size="small"
                  />
                  <FundsInvested
                    contributions={group.totalContributions}
                    progressPercentage={progressPercentage}
                    size="small"
                  />
                  {/* <StyledProgressWrapper>
                    <StyledProgressDataWrapper>
                      <Text fontWeight={500} fontSize={14} color={theme.text2}>
                        Funds invested
                      </Text>
                      <TYPE.body color={theme.text1} fontWeight={600} fontSize="16px">
                        {group.totalContributions} ETH
                      </TYPE.body>
                    </StyledProgressDataWrapper>
                    <StyledProgressBackground>
                      <StyledProgress percentageString={`${progressPercentage}%`} />
                    </StyledProgressBackground>
                  </StyledProgressWrapper> */}
                </StyledDataPointsWrapper>
              )}
              {/* Move this to details page */}
              <div css={listItemActionStyles}>
                {showStartButton ? (
                  <NavLink
                    to={`/group/${group.id}/start`}
                    css={listItemButtonStyles(theme)}
                    onClick={e => {
                      e.stopPropagation()
                      e.nativeEvent.stopImmediatePropagation()
                    }}
                  >
                    {t('Start')}
                  </NavLink>
                ) : null}
                {showAbortButton ? (
                  <NavLink
                    to={`/group/${group.id}/abort`}
                    css={listItemButtonStyles(theme)}
                    onClick={e => {
                      e.stopPropagation()
                      e.nativeEvent.stopImmediatePropagation()
                    }}
                  >
                    {t('Abort')}
                  </NavLink>
                ) : null}
                {showJoinButton ? (
                  <NavLink
                    to={{
                      pathname: `/group/${group.id}/join`,
                      state: {
                        backPath: pathName
                      }
                    }}
                    css={listItemButtonStyles(theme)}
                    onClick={e => {
                      e.stopPropagation()
                      e.nativeEvent.stopImmediatePropagation()
                    }}
                  >
                    {t('Join')}
                  </NavLink>
                ) : null}
                {showAddFundsButton ? (
                  <NavLink
                    to={`/group/${group.id}/add-funds`}
                    css={listItemButtonStyles(theme)}
                    onClick={e => {
                      e.stopPropagation()
                      e.nativeEvent.stopImmediatePropagation()
                    }}
                  >
                    {t('Add Funds')}
                  </NavLink>
                ) : null}
                {showFinishButton ? (
                  <NavLink
                    to={`/group/${group.id}/finish`}
                    css={listItemButtonStyles(theme)}
                    onClick={e => {
                      e.stopPropagation()
                      e.nativeEvent.stopImmediatePropagation()
                    }}
                  >
                    {t('Finish')}
                  </NavLink>
                ) : null}
                {showWithdrawButton ? (
                  <NavLink
                    to={`/group/${group.id}/withdraw`}
                    css={listItemButtonStyles(theme)}
                    onClick={e => {
                      e.stopPropagation()
                      e.nativeEvent.stopImmediatePropagation()
                    }}
                  >
                    {t('Withdraw')}
                  </NavLink>
                ) : null}
                {showSwapButton ? (
                  <NavLink
                    to={`/group/${group.id}/swap`}
                    css={listItemButtonStyles(theme)}
                    onClick={e => {
                      e.stopPropagation()
                      e.nativeEvent.stopImmediatePropagation()
                    }}
                  >
                    {t('Swap')}
                  </NavLink>
                ) : null}
              </div>
              {/* <div
                css={css`
                  position: absolute;
                  top: calc(50% - 10px);
                  right: 10px;
                  opacity: 0.3;
                `}
              >
                <img src={RightArrowFlat} alt="details" style={{ width: '20px' }} />
              </div> */}
            </StyledFundCardWrapper>
          )
        })}
      </div>
    </div>
  )
}
