import React, { useContext, useRef } from 'react'
import styled, { ThemeContext } from 'styled-components'
import { darken } from 'polished'
import { useTranslation } from 'react-i18next'
import { NavLink, Link as HistoryLink, RouteComponentProps, withRouter } from 'react-router-dom'

import { ArrowLeft, Share } from 'react-feather'
import { RowBetween } from '../Row'
import QuestionHelper from '../QuestionHelper'
import {
  FacebookShareButton,
  FacebookIcon,
  RedditShareButton,
  RedditIcon,
  TelegramShareButton,
  TelegramIcon,
  TwitterShareButton,
  TwitterIcon,
  WhatsappShareButton,
  WhatsappIcon
} from 'react-share'
import useToggle from '../../hooks/useToggle'
import { useOnClickOutside } from '../../hooks/useOnClickOutside'

const Tabs = styled.div`
  ${({ theme }) => theme.flexRowNoWrap}
  align-items: center;
  border-radius: 3rem;
  justify-content: space-evenly;
  @media (max-width: 575.98px) {
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
  }
`

const StyledTabs = styled(Tabs)`
  ${({ theme }) => theme.flexRowNoWrap}
  align-items: center;
  border-radius: 3rem;
  justify-content: space-evenly;

  ${({ theme }) => theme.mediaWidth.upToMedium`
    flex-flow: column;
    align-items: start;
  `};
`

const StyledBackHeadingWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-bottom: ${({ theme }) => `1px solid ${theme.bg3}`};
  border-radius: 1rem 1rem 0 0;
  background: ${({ theme }) => theme.bg2};
  width: 100%;
  max-width: 800px;
`

// const StyledHistoryLink = styled(HistoryLink)`
//   height: 1.5rem;
// `

const activeClassName = 'ACTIVE'

export const StyledNavLink = styled(NavLink).attrs({
  activeClassName
})`
  ${({ theme }) => theme.flexRowNoWrap}
  position: relative;
  align-items: center;
  justify-content: center;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  color: ${({ theme }) => theme.text3};
  font-size: 16px;
  font-weight: 600;
  padding-left: 1.5rem;

  &.${activeClassName} {
    color: ${({ theme }) => theme.text1};
  }

  :hover,
  :focus {
    color: ${({ theme }) => darken(0.1, theme.text1)};
  }

  ${({ theme }) => theme.mediaWidth.upToMedium`
    padding-left: 0;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    border-bottom: ${({ theme }) => `1px solid ${theme.bg4}`};
    justify-content: start;
    width: 100%;
  `};
`

const ActiveText = styled.div`
  font-weight: 600;
  font-size: 16px;
`

const StyledShareWrapper = styled.div`
  position: relative;
`

const StyledShareButtonsWrapper = styled.div`
  height: 2rem;
  display: flex;
  justify-content: space-between;
  width: 100%;
`

const MenuFlyout = styled.span`
  background-color: ${({ theme }) => theme.bg3};
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 0.5rem;
  padding: 1rem;
  font-size: 1rem;
  position: absolute;
  top: 2rem;
  right: 0rem;
  width: 208px;
  z-index: 100;

  ${({ theme }) => theme.mediaWidth.upToMedium`
    min-width: 12rem;
  `};
`

const StyledShareIcon = styled(Share)`
  cursor: pointer;
  transition: all 0.5s ease;

  :hover,
  :focus {
    outline: none;
    stroke: ${({ theme }) => theme.bg4};
  }
`

const StyledBackIcon = styled(ArrowLeft)`
  cursor: pointer;
  transition: all 0.5s ease;

  :hover,
  :focus {
    outline: none;
    stroke: ${({ theme }) => theme.bg4};
  }
`

const StyledSpacerDiv = styled.div`
  width: 24px;
`

export function GroupStakingTabs({ active }: { active: string }) {
  const { t } = useTranslation()

  return (
    <StyledTabs>
      <StyledNavLink
        id={`home-nav-link`}
        to={'/'}
        isActive={() => active === '' || active === 'open' || active === 'active' || active === 'ended'}
      >
        {t('Funds')}
      </StyledNavLink>
      <StyledNavLink id={`trades-nav-link`} to={'/trades'} isActive={() => active === 'trades'}>
        {t('Tokens')}
      </StyledNavLink>
      <StyledNavLink id={`staking-nav-link`} to={'/staking'} isActive={() => active === 'staking'}>
        {t('Stake')}
      </StyledNavLink>
      <StyledNavLink id={`leaderboard-nav-link`} to={'/leaderboard'} isActive={() => active === 'leaderboard'}>
        {t('Leaderboard')}
      </StyledNavLink>
    </StyledTabs>
  )
}

export function MainTabs({ items, active }: { items: any; active: string }) {
  const { t } = useTranslation()
  return (
    <Tabs style={{ marginBottom: '20px' }}>
      {items.map((item: any) => {
        return (
          <StyledNavLink key={item.key} to={item.path} isActive={() => active === item.key}>
            {t(item.title)}
          </StyledNavLink>
        )
      })}
    </Tabs>
  )
}

export function FindPoolTabs() {
  return (
    <Tabs>
      <RowBetween style={{ padding: '1rem' }}>
        <HistoryLink to="/pool">
          <ArrowLeft />
        </HistoryLink>
        <ActiveText>Import Pool</ActiveText>
        <QuestionHelper text={"Use this tool to find pairs that don't automatically appear in the interface."} />
      </RowBetween>
    </Tabs>
  )
}

interface HeadingProps {
  backPath?: any
  title: string
  helperText?: string
  showShare?: boolean
}

export function TheMainHeading({
  history,
  location,
  backPath,
  title,
  helperText,
  showShare
}: RouteComponentProps & HeadingProps) {
  const theme = useContext(ThemeContext)

  const node = useRef<HTMLDivElement>()
  const [open, toggle] = useToggle(false)

  const shareUrl = location.pathname

  useOnClickOutside(node, open ? toggle : undefined)

  return (
    <StyledBackHeadingWrapper>
      <RowBetween style={{ padding: '1rem' }}>
        {backPath ? <StyledBackIcon color={theme.primary1} onClick={() => history.goBack()} /> : null}
        <ActiveText>{title}</ActiveText>
        {!helperText && !showShare && <StyledSpacerDiv />}
        {helperText && <QuestionHelper text={helperText} />}
        {showShare && (
          <StyledShareWrapper ref={node as any}>
            <StyledShareIcon color={theme.primary1} onClick={toggle} />
            {open && (
              <MenuFlyout>
                <StyledShareButtonsWrapper>
                  <FacebookShareButton url={`https://app.unifund.global${shareUrl}`}>
                    <FacebookIcon size={32} borderRadius={4} />
                  </FacebookShareButton>
                  <RedditShareButton url={`https://app.unifund.global${shareUrl}`}>
                    <RedditIcon size={32} borderRadius={4} />
                  </RedditShareButton>
                  <TelegramShareButton url={`https://app.unifund.global${shareUrl}`}>
                    <TelegramIcon size={32} borderRadius={4} />
                  </TelegramShareButton>
                  <TwitterShareButton url={`https://app.unifund.global${shareUrl}`}>
                    <TwitterIcon size={32} borderRadius={4} />
                  </TwitterShareButton>
                  <WhatsappShareButton url={`https://app.unifund.global${shareUrl}`}>
                    <WhatsappIcon size={32} borderRadius={4} />
                  </WhatsappShareButton>
                </StyledShareButtonsWrapper>
              </MenuFlyout>
            )}
          </StyledShareWrapper>
        )}
      </RowBetween>
    </StyledBackHeadingWrapper>
  )
}

export const MainHeading = withRouter(TheMainHeading)
