import React, { useContext } from "react"
import { useTranslation } from "react-i18next"
import styled, { ThemeContext } from "styled-components"
import { AutoColumn, ColumnCenter } from "src/components/Column"
import { RowBetween } from "src/components/Row"
import { CloseIcon } from 'src/theme/components'
import { Text } from 'rebass'
import { AlertTriangle, ArrowUpCircle } from "react-feather"
import { ButtonPrimary } from 'src/components/Button'
import Modal from "../Modal"

const Wrapper = styled.div`
  width: 100%;`

const Section = styled(AutoColumn)`
  padding: 24px;
`
const BottomSection = styled(Section)`
  background-color: ${({ theme }) => theme.bg2};
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
`
const ConfirmedIcon = styled(ColumnCenter)`
  padding: 60px 0;
`
function ErrorModalContent({ message, onDismiss }: { message: string; onDismiss: () => void }) {
    const { t } = useTranslation()
    const theme = useContext(ThemeContext)
    return (
      <Wrapper>
        <Section>
          <RowBetween>
            <Text fontWeight={500} fontSize={20}>
              {t('Error')}
            </Text>
            <CloseIcon onClick={onDismiss} />
          </RowBetween>
          <AutoColumn style={{ marginTop: 20, padding: '2rem 0' }} gap="24px" justify="center">
            <AlertTriangle color={theme.red1} style={{ strokeWidth: 1.5 }} size={64} />
            <Text fontWeight={500} fontSize={16} color={theme.red1} style={{ textAlign: 'center', width: '85%' }}>
              {message}
            </Text>
          </AutoColumn>
        </Section>
        <BottomSection gap="12px">
          <ButtonPrimary onClick={onDismiss}>{t('Dismiss')}</ButtonPrimary>
        </BottomSection>
      </Wrapper>
    )
  }

  function SuccessModalContent({message, onDismiss}: { message: string; onDismiss: () => void }) {
    const { t } = useTranslation()
    const theme = useContext(ThemeContext)
  
    return (
      <Wrapper>
        <Section>
          <RowBetween>
            <div />
            <CloseIcon onClick={onDismiss} />
          </RowBetween>
          <ConfirmedIcon>
            <ArrowUpCircle strokeWidth={0.5} size={90} color={theme.primary1} />
          </ConfirmedIcon>
          <AutoColumn gap="12px" justify={'center'}>
            <Text fontWeight={500} fontSize={16}>
              {message}
            </Text>            
            <ButtonPrimary onClick={onDismiss} style={{ margin: '20px 0 0 0' }}>
              <Text fontWeight={500} fontSize={20}>
                {t('Close')}
              </Text>
            </ButtonPrimary>
          </AutoColumn>
        </Section>
      </Wrapper>
    )
  }

export default function InfoModal({
  isOpen,
  isError,
  message, 
  onDismiss} : { 
    isOpen: boolean
    isError: boolean
    message: string
    onDismiss: () => void }) {
      return (
        <Modal isOpen={isOpen} maxHeight={90} onDismiss={onDismiss}>
          {isError 
          ? <ErrorModalContent message={message} onDismiss={onDismiss} /> 
          : <SuccessModalContent message={message} onDismiss={onDismiss} />}        
        </Modal>)  
}
  




  
