import React, { useState } from 'react'
import DefaultLayout from 'src/layouts/default'
import { MainHeading } from 'src/components/NavigationTabs'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import { useGroupActions } from 'src/context/GroupContext'
import { css } from '@emotion/core'
import { ButtonError, ButtonPrimary } from 'src/components/Button'
import { PageSpinner } from 'src/components/PageSpinner'
import { TYPE } from 'src/theme'
import { ErrorCard } from 'src/components/Card'

const GroupStart = ({
  history,
  match: {
    params: { id }
  }
}: RouteComponentProps<{ id?: string }>) => {
  const { getGroup, startGroup } = useGroupActions()
  const [attempting, setAttempting] = useState(false)

  const group = getGroup(parseInt(id!))
  const started = group?.data.runTimeout > 0
  const status = group?.data.complete ? 'complete' : group?.data.aborted ? 'aborted' : started ? 'started' : 'created'

  const confirm = () => {
    startGroup(group?.id!)
    setAttempting(true)
  }

  const unstartable = group?.params.minEthToStart! > group?.totalContributions!

  return (
    <>
      <MainHeading backPath={'/'} title={'Start Fund'} />
      <DefaultLayout
        isChildPage
        css={css`
          max-width: 800px;
          width: 100%;
        `}
      >
        {unstartable ? (
          <>
            <ErrorCard>Starting balance not reached. Current balance: {group?.totalContributions} ETH</ErrorCard>
            <TYPE.body margin="1rem 0">
              The fund can start trading when it reaches {group?.params.minEthToStart} ETH. Add the necessary funds or
              wait for further contributions.
            </TYPE.body>
            <ButtonPrimary onClick={() => history.push(`/group/${group?.id}/add-funds`)} width="120px">
              Add Funds
            </ButtonPrimary>
          </>
        ) : group ? (
          <>
            {status === 'started' ? (
              <ErrorCard>Fund has already started.</ErrorCard>
            ) : (
              <ErrorCard>Minimum ETH to start: {group?.params.minEthToStart} ETH</ErrorCard>
            )}
            {status === 'created' ? (
              <>
                <h1>{"It's"} go time</h1>
                <TYPE.body margin="1rem 0">May the pump gods be with you.</TYPE.body>
                <ButtonError disabled={attempting} onClick={confirm} width="120px">
                  {attempting ? 'Starting...' : 'Start fund'}
                </ButtonError>
              </>
            ) : null}
          </>
        ) : (
          <PageSpinner />
        )}
      </DefaultLayout>
    </>
  )
}

export default withRouter(GroupStart)
