import React, { useState } from 'react'
import DefaultLayout from 'src/layouts/default'
import { MainHeading } from 'src/components/NavigationTabs'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import { useGroupActions } from 'src/context/GroupContext'
import { css } from '@emotion/core'
import { ButtonError } from 'src/components/Button'
import { PageSpinner } from 'src/components/PageSpinner'
import { ErrorCard } from 'src/components/Card'
import { TYPE } from 'src/theme'

const GroupWithdraw = ({
  history,
  match: {
    params: { id }
  }
}: RouteComponentProps<{ id?: string }>) => {
  const { getGroup, withdraw } = useGroupActions()
  const [attempting, setAttempting] = useState(false)

  const group = getGroup(parseInt(id!))
  const started = group?.data.runTimeout > 0
  const status = group?.withdrawn
    ? 'withdrawn'
    : group?.data.complete
    ? 'complete'
    : group?.data.aborted
    ? 'aborted'
    : started
    ? 'started'
    : 'created'

  const confirm = async () => {
    setAttempting(true)
    await withdraw(group?.id!)
    setAttempting(false)
  }

  return (
    <>
      <MainHeading backPath={'/'} title={'Withdraw funds'} />
      <DefaultLayout
        isChildPage
        css={css`
          max-width: 800px;
          width: 100%;
        `}
      >
        {group ? (
          <>
            {status === 'withdrawn' ? <ErrorCard>Funds have been withdrawn.</ErrorCard> : null}
            {status === 'created' ? (
              <ErrorCard>Fund must be ended or aborted before funds can be withdrawn.</ErrorCard>
            ) : null}
            {status === 'started' ? (
              <ErrorCard>Fund must be ended or aborted before funds can be withdrawn.</ErrorCard>
            ) : null}
            {status === 'complete' || status === 'aborted' ? (
              <>
                <h1>Are you sure?</h1>
                <TYPE.body margin="1rem 0">There is no going back.</TYPE.body>
                <ButtonError disabled={attempting} onClick={confirm}>
                  {attempting ? 'Withdrawing...' : 'Withdraw funds'}
                </ButtonError>
              </>
            ) : null}
          </>
        ) : (
          <PageSpinner />
        )}
      </DefaultLayout>
    </>
  )
}

export default withRouter(GroupWithdraw)
