import React, { useContext } from 'react'
import styled, { ThemeContext } from 'styled-components'
import { TYPE } from '../../theme'

import { LightCard } from '../Card'
import { AutoColumn } from '../Column'
import Slider from '../Slider'
import QuestionHelper from '../QuestionHelper'
import { Input } from '../Input'
import { FlexBox } from '../FlexBox'

const StyledInputWrapper = styled.div`
  position: relative;
`

const StyledUnitWrapper = styled.div`
  position: absolute;
  top: 1rem;
  right: 0.75rem;
`

const StyledFlexBox = styled(FlexBox)`
  ${({ theme }) => theme.mediaWidth.upToSmall`
    flex-flow: column;
  `};
`

const StyledBalanceMax = styled.button`
  height: 28px;
  background-color: transparent;
  border: 1px solid ${({ theme }) => theme.primary1};
  border-radius: 0.25rem;
  font-size: 0.875rem;

  font-weight: 400;
  cursor: pointer;
  margin-right: 0.5rem;
  color: ${({ theme }) => theme.primary1};
  :hover {
    background-color: ${({ theme }) => theme.primary4};
  }
  :focus {
    border: 1px solid ${({ theme }) => theme.primary1};
    outline: none;
  }

  ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    margin-right: 0.5rem;
  `};
`

interface InputPanelProps {
  value: number
  label?: string
  subLabel?: string
  helpText?: string
  unit?: string
  onChange: (value: number) => void
  onChangeInput: (e: React.ChangeEvent<HTMLInputElement>) => void
  step: number
  min: number
  max: number
  size?: number
  id: string
  disabled?: boolean
  onMax?: () => void
  showMaxButton?: boolean
}

export default function InputPanel({
  label,
  disabled,
  subLabel,
  helpText,
  value,
  onChange,
  onChangeInput,
  min,
  max,
  id,
  unit,
  step,
  onMax,
  showMaxButton
}: InputPanelProps) {
  const theme = useContext(ThemeContext)

  return (
    <LightCard id={id}>
      <AutoColumn gap="20px">
        <FlexBox justify="space-between" align="center">
          <StyledFlexBox align="baseline">
            <TYPE.main color={theme.text1} fontSize="14px" marginRight="0.5rem">
              {label}
            </TYPE.main>
            {subLabel && <TYPE.body fontSize="14px">{subLabel}</TYPE.body>}
          </StyledFlexBox>
          {showMaxButton && <StyledBalanceMax onClick={onMax}>MAX</StyledBalanceMax>}
          {helpText && <QuestionHelper text={helpText} />}
        </FlexBox>
        <StyledInputWrapper>
          {unit && (
            <StyledUnitWrapper>
              <TYPE.main fontSize="1.25rem">{unit}</TYPE.main>
            </StyledUnitWrapper>
          )}
          <Input
            disabled={disabled}
            type="number"
            fontSize="1.5rem"
            padding="0.875rem"
            value={value}
            onChange={onChangeInput}
          />
        </StyledInputWrapper>
        <Slider disabled={disabled} value={value} min={min} max={max} onChange={onChange} step={step} />
      </AutoColumn>
    </LightCard>
  )
}
